<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>单据日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>虚拟包裹</span>
                        <el-select
                                v-model="queryData.packStatus"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.packType"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                v-model="queryData.syncStatus"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.syncStatusList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.customer" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>订单ID</span>
                        <el-input v-model="queryData.orderId" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    stripe
                    border
                    ref="multipleSelection"
                    :data="tableData"
                    :height="tableHeight"
                    :summary-method="getSummaries"
                    show-summary>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        :label="tableKey[1]">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[2]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="currency_cn"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="is_virtual"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="package_no"
                        :label="tableKey[5]"
                        width="200">
                    <template slot-scope="scope">
                        <div>
                            <span @click="handleQueryDetailInfo(scope.row.package_no)" class="ui-link">{{scope.row.package_no}}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="customer"
                        :label="tableKey[6]"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="refund_date"
                        :label="tableKey[7]"
                        width="180">
                </el-table-column>
                <el-table-column
                        :label="tableKey[8]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总数量：{{pageData.totalQuantity}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <ComDialogTable
                title="明细信息"
                :showDialog="showDialog"
                :list="dialogTableData"
                :tableHeight="tableHeight"
                :topInfo="topInfo"
                :pageData="dialogTablePage"
                @close="handleCancelDialog"
                @tablePageToggle="handleToggleDialogPage"></ComDialogTable>
    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getSalesRefundOrderInfo,
        getSalesRefundDetailInfo
    } from '../../../api/marketing_management/sales_refund_order';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                optionList: {
                    packType:[
                        {value:'',label:"全部"},
                        {value:"0",label:"否"},
                        {value:"1",label:"是"},
                    ],
                    syncStatusList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未同步"},
                        {value: "1", label: "已同步"},
                        {value: "2", label: "同步失败"}
                    ],
                },
                queryData: {
                    proofDate: [],
                    packStatus: '',
                    syncStatus: '',
                    customer: '',
                    docNo: '',
                    orderId: '',
                },
                saveDefaultDate:[],

                tableKey:["业务日期", "单据编号", "虚拟包裹", "单据编号", "往来单位", "SKU", "出库数量", "币别", "单价", "金额", "K3单据号", "推送日期"],
                tableData: [],

                //总金额
                totalPrice:'',

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 订单号
                order_no: '',
                countData: {},
                coinTypeList: {},

                // 显示dialogTable
                showDialog: false,
                dialogTableData: {},
                topInfo: [],
                saveDocNo: '',
                dialogTablePage: {
                    offset: 1,
                    limit: 20,
                    total: 0
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'salesRefundInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSalesRefundOrderInfo'
            ]),
            getSalesRefundOrderInfo,
            getSalesRefundDetailInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries() {
                let sums = [];
                sums[0] = '总计';
                // sums[6] = this.countData ? Math.ceil(this.countData.total_qty * 100) / 100 : '';
                // sums[8] = this.countData ? Math.ceil(this.countData.total_price * 100) / 100 : '';
                // sums[9] = this.countData ? (Math.ceil(this.countData.total_amount * 100) / 100) : '';
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    packStatus: '',
                    syncStatus: '',
                    customer: '',
                    docNo: '',
                    orderId: '',
                }
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sales_refund']);
                }

                let obj = {
                    cbegintime: this.queryData.proofDate[0],
                    cendtime: this.queryData.proofDate[1],
                    bill_no: this.queryData.docNo,
                    status: this.queryData.syncStatus,
                    virtual_package: this.queryData.packStatus,
                    contacts: this.queryData.customer,
                    order_id: this.queryData.orderId,
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'sales_refund',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(obj || {}, params);

                this.GetSalesRefundOrderInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1,
                    total: this.pageData.currentTotal
                }, true);
            },

            // 查询明细信息
            handleQueryDetailInfo(docNo, params) {
                this.showDialog = true;
                let obj = {
                    bill_no: docNo
                };
                this.saveDocNo = docNo;
                obj = Object.assign(obj, params || {});

                this.getSalesRefundDetailInfo(obj).then(({ data }) => {
                    this.dialogTableData = data.data_list;
                    this.dialogTableData.props = ['package_no', 'sku', 'qty', 'price', 'amount'];
                    this.topInfo.splice(0);
                    this.topInfo.push(
                        { label: '总金额:', value: data.total_amount },
                        { label: '总数量:', value: data.total_qty }
                    );
                    this.dialogTablePage = data.page_data;
                }).catch(err => {
                    console.log(err)
                })
            },

            // 关闭弹框
            handleCancelDialog() {
                this.showDialog = false;
                this.topInfo.splice(0);
            },

            // 切换弹框表格分页
            handleToggleDialogPage({offset, limit}) {
                this.handleQueryDetailInfo(this.saveDocNo, {
                    limit: limit,
                    offset: offset
                })
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                this.optionList.syncStatusList = data.select_list.push_status;
                this.optionList.packType = data.select_list.virtual_package;
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.salesRefundInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['sales_refund']);
                        this.handleQueryInfoCallBack(this.salesRefundInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>
    @import (reference) '../../../assets/less/index';
    .ui-link{
        cursor: pointer;
        color: @base-link-color;
    }
</style>


