<template>
    <div class="ui-upload">
        <el-upload
                :show-file-list="false"
                :action="url"
                :on-change="handleChange"
                :on-success="autoUploadSuccess"
                :on-error="autoUploadError">
            <el-button size="medium" type="primary">导入退款</el-button>
        </el-upload>
    </div>
</template>

<script>
    export default {
        name: "ComUpload",
        props: {
            url: {
                type: String,
                default: '/'
            },
            title: '上传'
        },
        data() {
            return {

            }
        },
        methods: {
            handleChange(file, fileList) {
                console.log(file, fileList)
            },

            // 上传成功
            autoUploadSuccess(res) {
                if (res.status) {
                    this.$message({
                        type: 'success',
                        message: '上传成功'
                    });
                    this.$emit('success', 1)
                } else {
                    let str = '';
                    for (let i = 0; i < res.msg_data.length; i++) {
                        str += `<div>${res.msg_data[i]}</div>`;
                    }
                    this.$alert(str, res.msg || '错误原因', {
                        customClass: 'ui-error-list',
                        dangerouslyUseHTMLString: true
                    });
                    this.$message({
                        type: 'error',
                        message: '上传失败'
                    })
                }
            },

            // 上传失败
            autoUploadError(err) {
                this.$message({
                    type: 'error',
                    message: err || '上传失败'
                })
            }
        }
    }
</script>

<style scoped>
    .ui-upload {
        display: inline-block;
        margin-right: 10px;
    }
</style>


