import Vue from 'vue'
import Store from '@/store'
// import { LoginByUserName } from '../store/actions'
import Router from 'vue-router'
import LoginPage from '../views/login/Login'
import Layout from '../views/HomePage/Layout'
import SalesOrder from '../views/HomePage/MarketingManagement/SalesPage'
import ReceivablesPage from '../views/HomePage/CashierManagement/ReceivablesPage'
import ReceiptPage from '../views/HomePage/CashierManagement/ReceiptPage'
import PayableRealityPage from '../views/HomePage/CashierManagement/PayableRealityPage'
import PayableEstimatePage from '../views/HomePage/CashierManagement/PayableEstimatePage'
import PayableDifferencePage from '../views/HomePage/CashierManagement/PayableDifferencePage'
import RefundPage from '../views/HomePage/CashierManagement/RefundPage'
import SalesFormPage from '../views/HomePage/ReportForms/SalesFormPage'
import CustomerFormPage from '../views/HomePage/ReportForms/CustomerFormPage'
import PurchaseOrderPage from '../views/HomePage/PurchaseManagement/PurchaseOrderPage'
import PurchaseInstockPage from '../views/HomePage/PurchaseManagement/PurchaseInstockPage'
import PurchaseReturnPage from '../views/HomePage/PurchaseManagement/PurchaseReturnPage'
import PurchaseDiscountPage from '../views/HomePage/PurchaseManagement/PurchaseDiscountPage'
import PurchaseFreightPage from '../views/HomePage/PurchaseManagement/PurchaseFreightPage'
import PurchasePaymentPage from '../views/HomePage/CashierManagement/PurchasePaymentPage'
import PurchaseRefundPage from '../views/HomePage/CashierManagement/PurchaseRefundPage'
import CostReceivableFormPage from '../views/HomePage/ReportForms/CostReceivableFormPage'
import PayableRealityFormPage from '../views/HomePage/ReportForms/PayableRealityFormPage'
import DirectAllotPage from '../views/HomePage/StorageManagement/DirectAllotPage'
import InventoryProfitOrderPage from '../views/HomePage/StorageManagement/InventoryProfitOrderPage'
import InventoryLossesOrderPage from '../views/HomePage/StorageManagement/InventoryLossesOrderPage'
import SalesSumFormPage from '../views/HomePage/ReportForms/SalesSumFormPage'
import ReceiptSumFormPage from '../views/HomePage/ReportForms/ReceiptSumFormPage'
import PayableEstimateFormPage from '../views/HomePage/ReportForms/PayableEstimateFormPage'
import OtherInStockPage from '../views/HomePage/StorageManagement/OtherInStockPage'
import OtherOutStockPage from '../views/HomePage/StorageManagement/OtherOutStockPage'
import SalesRefundPage from '../views/HomePage/MarketingManagement/SalesRefundPage'
import OperateSummaryPage from '../views/HomePage/OperateManagement/OperateSummaryPage'
import OperateAbnormalPage from '../views/HomePage/OperateManagement/OperateAbnormalPage'
import PushErrorLog from '../views/HomePage/ErrorLog/PushErrorLog'
import SumErrorLog from '../views/HomePage/ErrorLog/SumErrorLog'
import ExpensesFormPage from '../views/HomePage/ReportForms/ExpensesFormPage'
import NotAuthority from '../views/NotAuthority'
import * as storage from '@/utils/storage'
import buttonConfig from '../authority/button_config'
import CONFIG from '../assets/js/config'
import { getUserInfo } from '@/api/login'
import { getAddressModelParams } from '../services/index'

Vue.use(Router);

const router =  new Router({
    routes: [
        {
            path: '/login',
            name: 'LoginPage',
            component: LoginPage
        },
        {
            path: '/home',
            name: 'home',
            component: Layout,
            redirect: '/sales',
            children: [
                {
                    path: '/sales',
                    name: '销售出库',
                    component: SalesOrder  // 销售出库
                },
                {
                    path: '/receivables',
                    name: '应收单',
                    component: ReceivablesPage  // 应收单
                },
                {
                    path: '/receipt',
                    name: '收款单',
                    component: ReceiptPage  // 收款单
                },
                {
                    path: '/payreal',
                    name: '应付单（实际）',
                    component: PayableRealityPage  // 应付单（实际）
                },
                {
                    path: '/payeste',
                    name: '应付单（预估）',
                    component: PayableEstimatePage  // 应付单（预估）
                },
                {
                    path: '/paydiff',
                    name: '应付单（差额）',
                    component: PayableDifferencePage // 应付单（差额）
                },
                {
                    path: '/refund',
                    name: '收款退款单',
                    component: RefundPage // 收款退款单
                },
                {
                    path: '/sales_form',
                    name: '销售出库明细报表',
                    component: SalesFormPage
                },
                {
                    path: '/customer_form',
                    name: '客户退款明细报表',
                    component: CustomerFormPage
                },
                {
                    path: '/purchase_order',
                    name: '采购订单',
                    component: PurchaseOrderPage
                },
                {
                    path: '/purchase_in_stock',
                    name: '采购入库',
                    component: PurchaseInstockPage
                },
                {
                    path: '/purchase_return',
                    name: '采购退料',
                    component: PurchaseReturnPage
                },
                {
                    path: '/purchase_discount',
                    name: '采购折扣',
                    component: PurchaseDiscountPage
                },
                {
                    path: '/purchase_freight',
                    name: '采购运费',
                    component: PurchaseFreightPage
                },
                {
                    path: '/purchase_payment',
                    name: '采购付款单',
                    component: PurchasePaymentPage
                },
                {
                    path: '/purchase_refund',
                    name: '采购退款单',
                    component: PurchaseRefundPage
                },
                {
                    path: '/cost_receivable',
                    name: '费用应收单报表',
                    component: CostReceivableFormPage
                },
                {
                    path: '/payable_reality_form',
                    name: '实际应付单汇总报表',
                    component: PayableRealityFormPage
                },
                {
                    path: '/payable_estimate_form',
                    name: '预估应付单汇总报表',
                    component: PayableEstimateFormPage
                },
                {
                    path: '/sales_sum_form',
                    name: '销售出库汇总报表',
                    component: SalesSumFormPage
                },
                {
                    path: '/receipt_sum_form',
                    name: '收款汇总报表',
                    component: ReceiptSumFormPage
                },
                {
                    path: '/direct_allot',
                    name: '直接调拨单',
                    component: DirectAllotPage
                },
                {
                    path: '/inventory_profit',
                    name: '盘盈单',
                    component: InventoryProfitOrderPage
                },
                {
                    path: '/inventory_losses',
                    name: '盘亏单',
                    component: InventoryLossesOrderPage
                },
                {
                    path: '/other_in_stock',
                    name: '其他入库单',
                    component: OtherInStockPage
                },
                {
                    path: '/other_out_stock',
                    name: '其他出库单',
                    component: OtherOutStockPage
                },
                {
                    path: '/sales_refund_order',
                    name: '销售退货',
                    component: SalesRefundPage
                },
                {
                    path: '/operate_sum',
                    name: '汇总',
                    component: OperateSummaryPage
                },
                {
                    path: '/operate_abnormal',
                    name: '异常处理',
                    component: OperateAbnormalPage
                },
                {
                    path: '/push_error',
                    name: '推送错误',
                    component: PushErrorLog
                },
                {
                    path: '/sum_error',
                    name: '汇总错误',
                    component: SumErrorLog
                },
                {
                    path: '/notAuthority',
                    name: '无权限界面',
                    component: NotAuthority
                },
                {
                    path: '/expenses_form',
                    name: '费用报表',
                    component: ExpensesFormPage
                }
            ]
        },
        {
            path: '*',
            redirect: '/home'
        }
    ]
});

let isFirstRequset = false;

router.beforeEach((to, from, next) => {
    const path = to.path;
    const isLogin = storage.getSessionStorage('userInfo');
    const queryParams = getAddressModelParams();

    if (!isLogin && !isFirstRequset) {
        // 如果没有登录信息的话在这里调下初始化接口，查询登录信息后保存
        return new Promise(resolve => {
            getUserInfo({
                access_token: queryParams['access_token']
            }).then(({ data }) => {
                if (process.env.NODE_ENV == 'production') {
                    CONFIG.HOME_PATH =  data.data.path;
                }
                Store.commit('SAVE_LOGIN_INFO', data.data);
                storage.setSessionStorage('userInfo', data.data ? JSON.stringify(data.data) : '');
                Store.commit('SAVE_PERMIT_PATH_INFO', data.data.permissions);
                Store.commit('SAVE_PERMIT_RESOURCE_INFO', data.data.permissions);
                resolve(1)
            });
        }).then(( data ) => {
            isFirstRequset = true;
            if (data) {
                next({
                    path: '/sales'
                });
                Store.commit('SAVE_TAB_LABELS', {
                    path: '/sales',
                    name: '销售出库订单',
                    index: '1-1'
                });
            }
        });
    } else if (isLogin || isFirstRequset) {
        let isAuthority = Store.getters.permitPath;
        if (isAuthority.indexOf(path) === -1 && path !== '/notAuthority') {
            return next({
                path: '/notAuthority'
            })
        }
        next();
    }
});

router.afterEach((to, from) => {
    Store.commit('SAVE_BUTTON_CONFIG', to.path);
});

export default router;



