<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model='queryData.statusType'
                                placeholder='请选择'
                                value-key>
                            <el-option
                                    v-for="item in pushType"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>汇总状态</span>
                        <el-select
                                size="medium"
                                v-model='queryData.summaryStatus'
                                placeholder='请选择'
                                value-key>
                            <el-option
                                    v-for="item in summaryTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.contactUnit" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.searchProof" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.searchK3Proof" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    border
                    stripe
                    :data="tableData"
                    :height="tableHeight"
                    :summary-method="getSummaries"
                    show-summary>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        width="200"
                        :label="tableKey[1]">
                </el-table-column>
                <el-table-column
                        prop="transaction_id"
                        width="200"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        width="150"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="currency"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="bank_account"
                        width="150"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="order_price"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="fee"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="cost"
                        :label="tableKey[9]">
                </el-table-column>
                <el-table-column
                        prop="tax_fee"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        prop="promotion_fee"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        prop="other_fee"
                        :label="tableKey[12]">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        width="180"
                        :label="tableKey[13]">
                </el-table-column>
                <el-table-column
                        prop="receive_time"
                        width="180"
                        :label="tableKey[14]">
                </el-table-column>
                <el-table-column
                        :label="tableKey[15]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.summary_status">{{ scope.row.summary_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="summary_bill_no"
                        width="200"
                        :label="tableKey[16]">
                </el-table-column>
                <el-table-column
                        prop="summary_time"
                        width="180"
                        :label="tableKey[17]">
                </el-table-column>
                <el-table-column
                        :label="tableKey[18]">
                    <template slot-scope="scope">
                        <span :class="'ui-txt-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="sync_time"
                        width="180"
                        :label="tableKey[19]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        
        <div class="ui-main-module-page">
            <!-- 数据汇总字段 -->
            <!--<span class="ui-tip-txt">总金额：{{pageData.aggregateAmount}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>

    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getReceiptOrderInfo
    } from '@/api/cashier_management/receipt_order'
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                queryData: {
                    statusType: '0',
                    summaryStatus: '',
                    proofDate: [],
                    searchProof: '',
                    searchK3Proof: '',
                    contactUnit: ''
                },
                summaryTypeList: [
                    {value: '', label: "全部"},
                    {value: "0", label: "未汇总"},
                    {value: "1", label: "已汇总"},
                    {value: "2", label: "汇总失败"}
                ],

                pushType: [
                    {value: '', label: "全部"},
                    {value: "0", label: "未推送"},
                    {value: "1", label: "推送中"},
                    {value: "2", label: "已推送"}
                ],

                // 保存默认日期
                saveDefaultDate: [],
                
                tableData: [],
                tableKey: ["序号", "业务类型", "虚拟包裹", "业务日期", "单据编号", "往来单位", "币别", "应收金额", "手续费", "成交费", "税费", "推广费", "其他费用", "实收金额", "K3单据号", "推送日期", "状态"],

                //总金额合计
                totalAmount: '',

                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'receiptInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetReceiptOrderInfo'
            ]),
            getReceiptOrderInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries(){
                let sums = [];
                sums[0] = '总计';
                sums[6] = this.countData.total_amount;
                sums[7] = this.countData.total_order_price;
                sums[8] = this.countData.total_fee;
                sums[9] = this.countData.total_cost;
                sums[10] = this.countData.total_tax_fee;
                sums[11] = this.countData.total_promotion_fee;
                sums[12] = this.countData.total_other_fee;

                return sums;
            },
            handleQueryInfo(params, isFn) {
                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['receipt']);
                }

                let obj = {
                    cbegintime: this.queryData.proofDate[0] || '',
                    cendtime: this.queryData.proofDate[1] || '',
                    transaction_id: this.queryData.searchProof || '',
                    k3_no: this.queryData.searchK3Proof || '',
                    customer: this.queryData.contactUnit || '',
                    summary_status: this.queryData.summaryStatus,
                    isPuted: this.queryData.statusType

                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'receipt',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetReceiptOrderInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);

                }).catch(err => {
                    this.tableData = [];
                    let msg = "系统提示：ERROR CODE：" + err.response.status + "，ERROR TEXT：" + err.response.statusText;
                    if (err.response.status !== 401 && err.response.status !== 402) {
                        this.$message({
                            message: msg,
                            type: 'warning'
                        });
                    }
                });
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData.proofDate = this.saveDefaultDate;
                this.queryData.searchProof = '';
                this.queryData.searchK3Proof = '';
                this.queryData.contactUnit = '';
                this.queryData.statusType = '0';
                this.queryData.summaryStatus = '';

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },

            // 分页条数
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },

            // 分页
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true);
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.pushType = data.data_list.sync_status;
                this.totalAmount = data.page_data.total_amount;
                this.countData = data.page_data;
                !this.tableData.length ? this.noData = true : this.noData = false;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            },
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.receiptInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['receipt']);
                        this.handleQueryInfoCallBack(this.receiptInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

</style>


