<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>调拨类型</span>
                        <el-select
                                size="medium"
                                v-model="queryData.allotType"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.allotTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.statusType"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.pushTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>SKU</span>
                        <el-input v-model="queryData.skuNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>调出仓库</span>
                        <el-input v-model="queryData.outStock" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>调入仓库</span>
                        <el-input v-model="queryData.inStock" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>订单ID</span>
                        <el-input v-model="queryData.orderNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo()">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>

        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    :height="tableHeight"
                    border
                    stripe
                    :summary-method="getSummaries"
                    show-summary>
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        :label="tableKey[1]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        :label="tableKey[2]"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        :label="tableKey[3]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[4]"
                        width="180">
                </el-table-column>
                <!-- <el-table-column
                  prop="qty"
                  :label="tableKey[5]">
                </el-table-column> -->
                <el-table-column
                        prop="allot_type_cn"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="allot_time"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="sku"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="real_qty"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="warehouse_in_code"
                        :label="tableKey[9]">
                </el-table-column>
                <el-table-column
                        prop="warehouse_out_code"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        prop="creater"
                        width="100"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        prop="sync_date"
                        width="100"
                        :label="tableKey[12]">
                </el-table-column>
                <el-table-column
                        width="100"
                        :label="tableKey[13]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
                <!-- 推送操作 -->
                <!--<el-table-column prop="operate" label="操作" width="120">-->
                <!--<template slot-scope="scope">-->
                <!--<el-button size="mini" v-show="scope.row.sync_status === '0'"-->
                <!--type="primary" plain @click="handlePush(scope.$index, scope.row)">推送-->
                <!--</el-button>-->
                <!--<el-button size="mini" v-show="scope.row.sync_status === '2'"-->
                <!--type="danger" plain @click="handleDeletePush(scope.$index, scope.row)">删除推送-->
                <!--</el-button>-->
                <!--</template>-->
                <!--</el-table-column>-->
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总数量：{{pageData.totalQuantity}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog
                center width="450px"
                title="推送单据"
                :visible.sync="isPushShow"
                :close-on-click-modal="false">
            <div>
                <div class="ui-com-center">
                    <span class="ui-com-pr">单据日期</span>
                    <el-date-picker
                            v-model="pushData.proofDate"
                            type='date'
                            value-format='yyyy-MM-dd'
                            placeholder="选择日期">
                    </el-date-picker>
                </div>
                <p class="ui-com-center ui-put-dialog" style="margin-top: 8px;">请你选择一个单据的生成日期</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancelPush()">放 弃</el-button>
                <el-button type="primary" @click="handleConfirmPush()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import {
        mapGetters
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDirectAllotFormInfo,
    } from '../../../api/storage_management/direct_allot_order';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,

                // 查询下拉列表
                optionList: {
                    pushTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未推送"},
                        {value: "1", label: "推送中"},
                        {value: "2", label: "已推送"}
                    ],
                    allotTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "否"},
                        {value: "1", label: "是"},
                    ]
                },

                // 查询参数
                queryData: {
                    proofDate: [],
                    allotType: '',
                    statusType: '',
                    orderNo: '',
                    skuNo: '',
                    docNo: '',
                    outStock: '',
                    inStock: ''
                },

                //多选数组
                tableSelectCheck: [],

                //定时推送数据
                // setTimePushData: {
                //     timePush: false,
                //     saveHint: false
                // },

                // 推送状态
                isPushShow: false,

                //推送数据
                pushData: {
                    proofDate: '',
                    ids: ''
                },

                //设置默认时间
                saveDefaultDate: [],

                //推送显示切换
                // putoff: false,

                //关键字检索
                // search_key: '',

                // 列表数据
                tableKey: [],
                tableData: [],

                //总订单合计
                totalOrders: '',
                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo'
            ])
        },
        methods: {
            getDirectAllotFormInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries(){
                let sums = [];
                sums[1] = '总计';
                sums[9] = this.countData.total_qty;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['direct_allot']);
                }

                let obj = {
                    cbegintime: this.queryData.proofDate[0],
                    cendtime: this.queryData.proofDate[1],
                    order_id: this.queryData.orderNo,
                    allot_type: this.queryData.allotType,
                    sku: this.queryData.skuNo,
                    billno: this.queryData.docNo,
                    warehouse_out_id: this.queryData.outStock,
                    warehouse_in_id: this.queryData.inStock,
                    sync_status: this.queryData.statusType
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'direct_allot',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.getDirectAllotFormInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;

                    // 判断是否没数据
                    this.isNotData = !data.data_list.value.length;

                    // 表格数据渲染
                    this.tableData = data.data_list.value;
                    this.tableKey = data.data_list.key;
                    this.totalOrders = data.total_qty;
                    this.countData = data.page_data;

                    this.optionList.pushTypeList = data.data_list.sync_status;
                    this.optionList.allotTypeList = data.data_list.allot_type;

                    //获取分页信息
                    this.pageData.currentList = data.page_data.limit;
                    this.pageData.currentPage = data.page_data.offset;
                    this.pageData.currentTotal = data.page_data.total;
                }).catch(err => {
                    console.log(err);
                });
            },

            /** 推送功能暂时注释保留 **/

            // //推送 按钮方法
            // handlePush: function (idx, row) {
            //
            //     //单行推送
            //     if (row) {
            //         this.isPushShow = true;
            //         this.pushData.ids = row.id;
            //     } else {
            //         if (!this.tableSelectCheck.length) return;
            //         this.isPushShow = true;
            //
            //         //多行推送
            //         if (this.tableSelectCheck.length) {
            //             let len = this.tableSelectCheck.length;
            //             let paramsArr = [];
            //             for (let i = 0; i < len; i++) {
            //
            //                 //筛选未推送数据
            //                 if (this.tableSelectCheck[i].sync_status === "0") {
            //                     paramsArr.push(this.tableSelectCheck[i].id);
            //                 }
            //             }
            //             this.pushData.ids = paramsArr.join(',');
            //         }
            //     }
            // },
            //
            // //确认 推送方法
            // handleConfirmPush: function () {
            //
            //     let obj = {
            //         sync_time: this.pushData.proofDate,
            //         ids: this.pushData.ids
            //     };
            //
            //     this.handlePushOrder(obj).then(({data}) => {
            //
            //         if (data.status) {
            //             this.$message({
            //                 type: 'success',
            //                 message: '推送成功'
            //             });
            //             this.handleQueryInfo();
            //         } else {
            //             this.$message({
            //                 type: 'error',
            //                 message: data.errorMess || '推送失败'
            //             });
            //         }
            //     }).catch(err => {
            //         console.log(err);
            //     });
            //     this.isPushShow = false;
            // },
            //
            // // 取消推送方法
            // handleCancelPush: function () {
            //     this.isPushShow = false;
            //     this.pushData.proofDate = '';
            //     this.pushData.ids = '';
            // },
            //
            // //删除推送 按钮方法
            // handleDeletePush: function (idx, row) {
            //
            //     //单行推送删除
            //     if (row) {
            //         this.pushData.ids = row.id;
            //     } else {
            //         if (!this.tableSelectCheck.length) return;
            //
            //         //多行推送删除
            //         if (this.tableSelectCheck.length) {
            //             let len = this.tableSelectCheck.length;
            //             let paramsArr = [];
            //             for (let i = 0; i < len; i++) {
            //
            //                 //筛选推送数据
            //                 if (this.tableSelectCheck[i].sync_status === "2") {
            //                     paramsArr.push(this.tableSelectCheck[i].id);
            //                 }
            //             }
            //             this.pushData.ids = paramsArr.join(',');
            //         }
            //     }
            //
            //     this.$confirm('执行后将该单据状态变成“未推送”，并且不支持删除K3数据，为了避免误操作，请您再次确定后点击【确定】', '提示', {
            //         confirmButtonText: '确定',
            //         cancelButtonText: '取消',
            //         type: 'warning',
            //         center: true
            //     }).then(() => {
            //
            //         let obj = {
            //             ids: this.pushData.ids
            //         };
            //         this.delPushOrder(obj).then(({ data }) => {
            //             if (data.status) {
            //                 this.$message({
            //                     type: 'success',
            //                     message: '删除推送成功!'
            //                 });
            //                 this.handleQueryInfo();
            //             } else {
            //                 this.$message({
            //                     type: 'error',
            //                     message: data.errorMess || '删除推送失败!'
            //                 });
            //             }
            //         }).catch(err => {
            //             return err;
            //         });
            //
            //     }).catch(() => {
            //         console.log('取消删除')
            //     });
            // },

            // //控制可否勾选项
            // autoCustomerSelect: function (row) {
            //     return row.is_virtual === '是' && row.sync_status !== '1' ? 1 : 0;
            // },

            //勾选方法
            handleSelectionChange: function (val) {
                this.tableSelectCheck = val;
                // console.log(this.tableSelectCheck)
            },

            // 重置
            handleResetQueryInfo: function () {

                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    allotType: '',
                    statusType: '',
                    orderNo: '',
                    skuNo: '',
                    docNo: '',
                    outStock: '',
                    inStock: ''
                };
            },

            // 分页
            handleSizeChange: function (size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },

            // 分页
            handleCurrentChange: function (idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            }
        },
        created: function () {
            this.thisVm = this;
            this.getDefaultTime().then(data => {
                this.queryData.proofDate.push(data.start, data.end);
                this.saveDefaultDate.push(data.start, data.end);
                this.handleQueryInfo();
            });
        },
        mounted: function () {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
</style>


