<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>单据日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.businessDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>单据类型</span>
                        <el-select
                                size="medium"
                                v-model="queryData.billType"
                                placeholder="请选择">
                            <el-option
                                    v-for="(item, index) in optionList.billTypeList"
                                    :key="item.index"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.billNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    stripe
                    ref="multipleSelection"
                    border
                    style="width: 100%"
                    class="ui-data-height"
                    :data="tableData"
                    :height="tableHeight">
                <el-table-column
                        type="selection"
                        width="40">
                </el-table-column>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        width="250"
                        :label="tableKey[1]">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        width="150"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="error_msg"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="sync_status"
                        :label="tableKey[4]"
                        width="180">
                    <template slot-scope="scope">
                        <span class="ui-state-color2">{{ scope.row.sync_status }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="date"
                        width="180"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="num"
                        width="80"
                        :label="tableKey[6]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getPushErrorInfo
    } from '../../../api/error_log/push_error';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'

    export default {
        data() {
            return {

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                isNotData: false,

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,

                // 查询下拉列表
                optionList: {
                    billTypeList: []
                },

                // 查询参数
                queryData: {
                    businessDate: [],
                    billType: '',
                    billNo: ''
                },

                //设置默认时间
                saveDefaultDate: [],

                // 列表数据
                tableKey: [],
                tableData: [],

                //总订单合计
                totalOrders: '',

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'pushErrorInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetPushErrorInfo'
            ]),
            getPushErrorInfo,
            getDefaultTime,
            autoAddTableInx,
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['push_error_log']);
                }

                let obj = {
                    cbegintime: this.queryData.businessDate[0],
                    cendtime: this.queryData.businessDate[1],
                    bill_type: this.queryData.billType,
                    bill_no: this.queryData.billNo
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'push_error_log',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetPushErrorInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            handleResetQueryInfo() {
                this.queryData = {
                    businessDate: this.saveDefaultDate,
                    billType: '',
                    billNo: ''
                };

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },

            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {
                // 表格数据渲染
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;

                this.optionList.billTypeList = data.drop_down_list.bill_type;

                this.isNotData = !data.data_list.value.length;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.pushErrorInfo) === '{}') {
                        this.queryData.businessDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['push_error_log']);
                        this.handleQueryInfoCallBack(this.pushErrorInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted: function () {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
</style>


