<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>单据日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>承运商</span>
                        <el-input v-model="queryData.carrier" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-main-module-table" v-if="initTable">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    :height="tableHeight"
                    :summary-method="getSummaries"
                    show-summary
                    stripe
                    border>
                <el-table-column
                        type="index"
                        width="80"
                        :index="autoAddTableInx"
                        :label="tableKey[0]">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        :label="tableKey[1]">
                </el-table-column>
                <el-table-column
                        prop="carrier_id"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="carrier_name"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        :label="tableKey[6]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getPaybleEstimateFormInfo
    } from '../../../api/report_forms/payble_estimate_form';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                // //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                queryData: {
                    proofDate: [],
                    docNo: '',
                    carrier: '',
                },
                saveDefaultDate:[],

                tableKey:["序号", "单据编号", "承运商id", "承运商名称", "往来单位", "金额", "创建时间"],
                tableData: [],
                countData:{},


                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'payableEstimateFormInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetPaybleEstimateFormInfo'
            ]),
            getPaybleEstimateFormInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries(){
                let sums = [];
                sums[0] = '总计';
                sums[5] = this.countData.total_amount;
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    docNo: '',
                    carrier: '',
                }
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['payable_estimate_form']);
                }

                let obj = {
                    bill_start_time: this.queryData.proofDate[0],
                    bill_end_time: this.queryData.proofDate[1],
                    bill_no: this.queryData.docNo,
                    carrier_id: this.queryData.carrier,

                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'payable_estimate_form',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetPaybleEstimateFormInfo(obj).then(({data}) =>{

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err =>{
                    console.log(err);
                });
            },
            //分页
            handleSizeChange(size){
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },

            //分页
            handleCurrentChange(idx){
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                //渲染数据
                this.tableKey = data.data_list.key;
                this.tableData = data.data_list.value;
                this.countData = data.page_data;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.payableEstimateFormInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['payable_estimate_form']);
                        this.handleQueryInfoCallBack(this.payableEstimateFormInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>
</style>


