<template>
  <div>
      <el-dialog title="删除推送" :visible.sync="delPushDialog.delPush" center width="450px" :close-on-click-modal="false">
      <div>
        <div class="ui-com-center">
          <span>执行后将该单据状态变成“未推送”，并且不支持删除K3数据，为了避免误操作，请您再次确定后点击【删除】</span>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelPush()">放 弃</el-button>
        <el-button type="danger" @click="confirmUnPushReceipt()">删 除</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        data () {
            return {

            };
        },
        props: ['delpushdialog'],
        computed: {
            delPushDialog () {
                return this.delpushdialog;
            }
        },
        methods: {
            confirmUnPushReceipt () {
                this.$emit('confirmDelPush');
            },
            cancelPush () {
                this.$emit('cancelDelPush');
            }
        }
    };
</script>



