const configFn = {
    navJumpFn(inx) {
        let inxArr = inx.split('-');
        let to = {};
        switch (inxArr.length) {
            case 1:
                to = config.navList[inxArr[0] - 1];
                break;
            case 2:
                to = config.navList[inxArr[0] - 1].children[inxArr[1] - 1];
                break;
            case 3:
                to = config.navList[inxArr[0] - 1].children[inxArr[1] - 1].children[inxArr[2] - 1];
                break;
        }
        return to
    }
};

export default configFn;


