<template>
    <div class="ui-main-module">
        <div class="ui-search-area">
            <div class='header-wrap'>
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model='push_state'
                                placeholder='请选择'
                                value-key>
                            <el-option v-for='item in sync_status' :key='item.value' :label='item.label'
                                       :value='item.value'>
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="carrier" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="receipt_no" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="k3_no" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" type="primary" @click="fnQuery()">查询</el-button>
                        <el-button @click="reSearch()" size="medium">重置</el-button>
                    </li>
                </ul>
            </div>
        </div>
        <div class="ui-fn-module">
            <el-button type="primary" size="medium" @click="push">推送</el-button>
            <el-button size="medium" @click="deletePush">删除推送</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    :data="tableData"
                    class="ui-data-height"
                    style="width: 100%"
                    slot="empty"
                    :height="tableHeight"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        :selectable="autoCustomerSelect"
                        width="55">
                </el-table-column>
                <el-table-column
                        type="index"
                        label="序号"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        label="业务类型"
                        width="180">
                    <template slot-scope="scope">
                        <span>应付单（差额物流费用）</span>
                    </template>
                </el-table-column>
                <el-table-column
                        width="80"
                        label="虚拟包裹">
                    <template slot-scope="scope">
                        <span>否</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="ship_time"
                        label="业务日期">
                </el-table-column>
                <el-table-column
                        prop="package_no"
                        label="单据编号">
                </el-table-column>
                <el-table-column
                        prop="carrier_name"
                        label="往来单位">
                </el-table-column>
                <!--<el-table-column-->
                <!--prop="customer"-->
                <!--label="结算组织">-->
                <!--</el-table-column>-->
                <el-table-column
                        prop="amount"
                        label="金额">
                </el-table-column>
                <el-table-column
                        prop="summary_bill_no"
                        label="K3单据号">
                </el-table-column>
                <el-table-column
                        prop="sync_time"
                        label="推送日期">
                </el-table-column>
                <el-table-column
                        label="状态">
                    <template slot-scope="scope">
                        <span>{{sync_statusObj[scope.row.sync_status  + '']}}</span>
                    </template>
                </el-table-column>

                <el-table-column
                        prop="operate"
                        label="操作">
                    <template slot-scope="scope">
                        <div v-show="isPushConfig">
                            <el-button
                                    size="mini"
                                    v-show="scope.row.sync_status  == '0'"
                                    type="primary"
                                    plain
                                    @click="push(scope.$index, scope.row)">推送
                            </el-button>
                            <el-button
                                    size="mini"
                                    v-show="scope.row.sync_status  == '2'"
                                    type="danger"
                                    plain
                                    @click="deletePush(scope.$index, scope.row)">删除推送
                            </el-button>
                        </div>
                    </template>
                </el-table-column>
                <div slot="empty">
                    <!--<img v-show="notdata" src="../../assets/notdata.jpg" alt="" class="center-block">-->
                </div>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总金额：{{pageData.aggregateAmount}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <!-- 删除推送对话框 -->
        <Delpush :delpushdialog="dialogData" @confirmDelPush="confirmUnPushReceipt"
                   @cancelDelPush="cancelPush"></Delpush>
        <!-- 推送对话框 -->
        <PushDialog :pushdialogdata="dialogData" @confirmPush="confirmPush" @cancelPush="cancelPush"></PushDialog>
        <!--提示组件 (推送中、推送成功、保存成功、删除推送、删除成功)-->
        <HintDialog :hintdialogdata="dialogData" @autoCloseHint="autoCloseHint"></HintDialog>
    </div>
</template>
<script>
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getPayableDifferenceInfo
    } from '@/api/cashier_management/payable_difference_order'

    export default {
        data() {
            return {
                initTable: false,
                tableHeight: 100,
                //初始化查询数据
                proofDate: [],
                defaultTime: [],
                sync_status: [{
                    value: '',
                    label: '全部'
                }, {
                    value: '0',
                    label: '未推送'
                }, {
                    value: '1',
                    label: '已推送'
                }, {
                    value: '2',
                    label: '推送中'
                }],
                //默认是已推送
                push_state: '0',
                sync_statusObj: {},
                receipt_no: '',
                carrier: '',
                k3_no: '',
                //请求loading
                Loading: false,
                //对话框弹框
                dialogData: {
                    pushShow: false,//推送
                    delPush: false,//删除推送
                    pushSuccess: false,//推送成功
                    pushFail: false,//推送失败
                    delFail: false,//删除失败
                    delSuccess: false//删除成功
                },
                countData:{},
                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },
                //其他数据
                checkIds: [],
                tableData: [],
                currentRow: '',
                isPushConfig: true,
                notdata: false,
                totalPrice: ''
            }
        },
        methods: {
            getDefaultTime,
            autoAddTableInx,
            getPayableDifferenceInfo,
            // getSummaries(){
            //     let sums = [];
            //     sums[1] = '总计';
            //     sums[6] = this.countData.total_amount;
            //     return sums;
            // },
            queryData: function (params) {
                let vm = this;
                this.getPayableDifferenceInfo(params).then(({ data }) => {
                    vm.tableData = data.data_list;
                    // vm.countData = data;
                    if (data.drop_down_box.push_status) {
                        vm.sync_status = data.drop_down_box.push_status;
                    }
                    vm.sync_statusObj = vm.creatObj(vm.sync_status);

                    //获取分页信息
                    this.pageData.currentList = data.page_data.limit;
                    this.pageData.currentPage = data.page_data.offset;
                    this.pageData.currentTotal = data.page_data.total;

                    vm.totalPrice = data.total_amount;
                    !data.data_list.length ? vm.notdata = true : vm.notdata = false;
                }).catch(err => {
                    vm.tableData = [];
                    let msg = "系统提示：ERROR CODE：" + err.response.status + "，ERROR TEXT：" + err.response.statusText;
                    if (err.response.status !== 401 && err.response.status !== 402) {
                        vm.$message({
                            message: msg,
                            type: 'warning'
                        });
                    }
                });
            },
            creatObj: function (obj) {
                let newObj = {};
                let length = obj.length;
                if (!length) return;
                for (let i = 0; i < length; i++) {
                    newObj[obj[i]["value"]] = obj[i]["label"]
                }
                return newObj;
            },
            fnQuery: function () {
                let params = {
                    bill_start_time: this.proofDate[0] || '',
                    bill_end_time: this.proofDate[1] || '',
                    bill_no: this.receipt_no || '',
                    carrier_id: this.carrier || '',
                    offset: this.currentPage,
                    limit: this.pageSize,
                    synstatus: this.push_state,
                    summary_bill_no: this.k3_no,
                    request_type: 1,
                };
                this.queryData(params);
            },
            reSearch: function () {
                this.proofDate = this.defaultTime;
                this.receipt_no = '';
                this.carrier = '';
                this.push_state = '0';
                this.k3_no = ''

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },
            handleSizeChange: function (size) {
                this.currentPage = 1;
                this.pageSize = size;
                this.fnQuery();
            },
            handleCurrentChange: function (val) {
                this.currentPage = val;
                this.fnQuery();
            },
            //触发勾选框事件
            handleSelectionChange(val) {
                this.checkIds = val
            },
            //推送
            push: function (parm, row) {
                if (this.interceptOperation(parm, row)) {
                    this.dialogData.pushShow = true;
                }
            },
            //删除推送
            deletePush: function (parm, row) {
                if (this.interceptOperation(parm, row)) {
                    this.dialogData.delPush = true;
                }
            },
            interceptOperation: function (parm, row) {
                if (!this.checkIds.length && !row) {
                    return false;
                }
                if (row) {
                    this.currentRow = row;
                    return true;
                }
                return true;
            },
            //确认推送
            confirmPush: function (data) {
                //关闭弹框
                this.dialogData.pushShow = false;
                //发起推送请求
                let vm = this;
                let params = {
                    sync_time: data,
                    request_type: 1
                };
                //过滤数据
                if (!this.currentRow) {
                    let arrId = this.filterArr(this.checkIds, true);
                    //拦截，提示失败
                    if (!arrId.length) {
                        this.dialogData.pushFail = true;
                        return;
                    }
                    params.ids = arrId.join(',');
                } else {
                    params.ids = this.currentRow.id;
                }
                this.$http.getAjax('/pay/diff_pay/manualPushDiff', params, vm).then(function ({data}) {
                    if (data.status) {
                        vm.dialogData.pushSuccess = true;
                        vm.fnQuery();
                    } else {
                        //提示推送失败
                        vm.dialogData.pushFail = true;
                    }
                }).catch(function (err) {
                    console.log(err);
                });
                this.resetData();
            },
            //确认删除推送单据
            confirmUnPushReceipt: function () {
                //关闭弹框
                this.dialogData.delPush = false;
                //发起推送请求
                let vm = this;
                let params = {
                    request_type: 1
                };
                //过滤推送数据
                if (!this.currentRow) {
                    let arrId = this.filterArr(this.checkIds, false);
                    //拦截，提示失败
                    if (!arrId.length) {
                        this.dialogData.delFail = true;
                        return;
                    }
                    params.ids = arrId.join(',');
                } else {
                    params.ids = this.currentRow.id;
                }
                this.$http.getAjax('/pay/diff_pay/deletePushDiff', params, vm).then(function ({data}) {
                    data.status ? vm.dialogData.delSuccess = true : vm.dialogData.delFail = true;
                    vm.fnQuery();
                }).catch(function () {
                    vm.dialogData.delFail = true;
                });
                //清空数据
                this.resetData();
            },
            filterArr: function (arr, isPush) {
                let length = arr.length;
                if (!length) return;
                let newArr = new Array();
                if (isPush) {
                    for (let i = 0; i < length; i++) {
                        let item = arr[i];
                        if (item.sync_status != "2" && item.sync_status != "1") {
                            newArr.push(item.id);
                        }
                    }
                } else {
                    for (let i = 0; i < length; i++) {
                        let item = arr[i];
                        if (item.sync_status != "0" && item.sync_status != "1") {
                            newArr.push(item.id);
                        }
                    }
                }
                return newArr;
            },
            resetData: function () {
                this.currentRow = '';
            },
            //关闭弹框
            cancelPush: function (parm, row) {
                if (this.dialogData.delPush) {
                    this.dialogData.delPush = false;
                }
                if (this.dialogData.pushShow) {
                    this.dialogData.pushShow = false;
                }
                this.resetData();
            },
            autoCloseHint: function (data) {
                this.dialogData[data] = false;
            },
            //控制可否勾选项
            autoCustomerSelect: function (row, idx) {
                if (row.sync_status !== '1') {
                    return 1;
                } else {
                    return 0;
                }
            }
        },
        created: function () {
            this.getDefaultTime().then(data => {
                this.queryData.businessDate.push(data.start, data.end);
                this.saveDefaultDate.push(data.start, data.end)
            });
            this.fnQuery();
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module', [
                'ui-search-area',
                'ui-fn-module',
                'ui-main-module-page'
            ]);
            this.initTable = true;
        }
    };
</script>
<style lang="less" scoped>
    @bg: rgba(204, 204, 204, 1);
    .el-select .el-input {
        width: 220px;
    }

    .el-table th > .cell,
    .el-table td > .cell {
        text-align: center;
    }

    .dialog-footer {
        padding: 0 12px;
    }

    .dialog-footer .ui-user-btn {
        width: 100%;
    }

    .dialog-footer .ui-user-hint {
        text-align: left;
        text-indent: 5px;
        border-radius: 4px;
        margin-bottom: 15px;
        box-sizing: border-box;
        background-color: #fa5555;
        line-height: 30px;
        font-size: 12px;
        color: #fff;
    }

    .el-dialog--center .el-dialog__body {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .ui-put-condition > p {
        height: 40px;
        line-height: 40px;
        clear: both;
    }

    .ui-put-condition ul {
        height: 40px;
    }

    .ui-put-condition ul li {
        line-height: 40px;
    }

    .ui-put-condition ul li .el-input {
        width: 150px;
    }

    .ui-put-condition ul li:first-child {
        padding-right: 15px;
    }

    .ui-put-condition ul li:nth-child(2) {
        width: 150px;
    }

    .ui-put-condition ul li:nth-child(3) {
        padding-left: 15px;
        width: 150px;
        color: #999;
    }

    .ui-put-condition ul li:last-child {
        padding-left: 15px;
        width: 150px;
    }

    .ui-put-dialog {
        color: #999;
        font-size: 12px;
    }

    .el-pagination {
        margin-top: 15px;
    }
</style>



