<template>
    <div>
        <!-- 推送对话框 -->
        <el-dialog
                center width="450px"
                title="推送单据"
                :visible.sync="pushDialogData.pushShow"
                :close-on-click-modal="false">
            <div>
                <div class="ui-com-center">
                    <span class="ui-com-pr">单据日期</span>
                    <el-date-picker
                            v-model="proofDate"
                            type='date'
                            value-format='yyyy-MM-dd'
                            placeholder="选择日期">
                    </el-date-picker>
                </div>
                <p class="ui-com-center ui-put-dialog" style="margin-top: 8px;">请你选择一个单据的生成日期</p>
            </div>
            <span slot="footer" class="dialog-footer">
        <el-button @click="cancelPush()">放 弃</el-button>
        <el-button type="primary" @click="confirmPush()">确 定</el-button>
      </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: 'PushDialog',
        data() {
            return {
                proofDate: ''
            };
        },
        props: ['pushdialogdata'],
        computed: {
            pushDialogData() {
                return this.pushdialogdata;
            }
        },
        methods: {
            confirmPush() {
                this.$emit('confirmPush', this.proofDate);
                this.proofDate = null;
            },
            cancelPush() {
                this.$emit('cancelPush');
                this.proofDate = null;
            }
        }
    };
</script>



