<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model='queryData.busType'
                                placeholder='请选择'
                                value-key>
                            <el-option
                                    v-for="item in optionList.syncStatusList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.contacts" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.billNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.k3BillNo" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-if="item.value !== 'import' || (item.value === 'import' && item.permit(thisVm))"
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    :data="tableData"
                    border
                    stripe
                    @selection-change="handleSelectionChange"
                    class="ui-data-height"
                    :summary-method="getSummaries"
                    show-summary
                    style="width: 100%"
                    :height="tableHeight">
                <el-table-column
                        fixed
                        type="selection"
                        :selectable="autoCustomerSelect"
                        width="40">
                </el-table-column>
                <el-table-column
                        fixed
                        type="index"
                        :label="tableKey[0]"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="transaction_id"
                        :label="tableKey[1]"
                        :width="tableColumnWidth.orderNo">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[2]"
                        :width="tableColumnWidth.orderNo">
                </el-table-column>
                <el-table-column
                        prop="platform_code"
                        width="80"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="platform_order_id"
                        :label="tableKey[4]"
                        :width="tableColumnWidth.orderNo">
                </el-table-column>
                <el-table-column
                        prop="account_id"
                        :label="tableKey[5]"
                        width="100">
                </el-table-column>
                 <el-table-column
                        prop="bank_account"
                        :label="tableKey[6]"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="currency"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="fee"
                        :label="tableKey[9]">
                </el-table-column>
                <el-table-column
                        prop="cost"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        prop="tax_fee"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        prop="promotion_fee"
                        :label="tableKey[12]">
                </el-table-column>
                <el-table-column
                        prop="other_fee"
                        :label="tableKey[13]">
                </el-table-column>
                <el-table-column
                        width="100"
                        prop="note"
                        :label="tableKey[14]">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        :width="tableColumnWidth.date"
                        :label="tableKey[15]">
                </el-table-column>
                <el-table-column
                        prop="refund_time"
                        :width="tableColumnWidth.date"
                        :label="tableKey[16]">
                </el-table-column>
                <el-table-column
                        :label="tableKey[17]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="sync_time"
                        :width="tableColumnWidth.date"
                        :label="tableKey[18]">
                </el-table-column>
                <el-table-column
                        :width="tableColumnWidth.button"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="medium"
                                v-show="scope.row.sync_status_cn === '未推送'"
                                type="primary"
                                plain
                                @click="handlePush(scope.$index, scope.row)">推送
                        </el-button>
                        <el-button
                                size="medium"
                                v-show="scope.row.sync_status_cn === '已推送' ||　scope.row.sync_status === '推送中'"
                                type="primary"
                                plain
                                @click="handleDeletePush(scope.$index, scope.row)">删除推送
                        </el-button>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

        <el-dialog
                center width="450px"
                title="推送单据"
                :visible.sync="isPushShow"
                :close-on-click-modal="false">
            <div>
                <div class="ui-com-center">
                    <span class="ui-com-pr">单据日期</span>
                    <el-date-picker
                            v-model="pushData.proofDate"
                            type='date'
                            value-format='yyyy-MM-dd'
                            placeholder="选择日期">
                    </el-date-picker>
                </div>
                <p class="ui-com-center ui-put-dialog" style="margin-top: 8px;">请你选择一个单据的生成日期</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancelPush()">放 弃</el-button>
                <el-button type="primary" @click="handleConfirmPush()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>

    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getRefundOrderInfo,
        handlePushOrder,
        delPushOrder
    } from '@/api/cashier_management/refund_order';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index';

    import * as importApi from '../../../api/import_api/index';
    import * as downloadApi from '../../../api/download_api/index';
    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                tableColumnWidth: {
                    date: 180,
                    orderNo: 200,
                    button: 120
                },

                // 查询下拉列表
                optionList: {
                    syncStatusList: [
                        {value:"",label:"全部"},
                        {value:"0",label:"未推送"},
                        {value:"1",label:"已推送"},
                        {value:"2",label:"推送失败"},
                    ]
                },

                // 查询参数
                queryData: {
                    proofDate: [],
                    busType: '0',
                    contacts: '',
                    billNo: '',
                    k3BillNo: ''
                },

                //多选数组
                tableSelectCheck: [],

                //定时推送数据
                // setTimePushData: {
                //     timePush: false,
                //     saveHint: false
                // },

                // 推送状态
                isPushShow: false,

                //推送数据
                pushData: {
                    proofDate: '',
                    ids: ''
                },

                //设置默认时间
                saveDefaultDate: [],

                // 列表数据
                tableKey: [],
                tableData: [],

                //总订单合计
                totalOrders: '',
                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'refundOrderInfo'
            ]),
            uploadUrl() {
                return importApi['IMPORT_REFUND_INFO'];
            },
            downloadUrl() {
                return downloadApi['DOWNLOAD_REFUND_MODEL']
            }
        },
        methods: {
            ...mapActions([
                'GetRefundOrderInfo'
            ]),
            getRefundOrderInfo,
            handlePushOrder,
            getDefaultTime,
            autoAddTableInx,
            delPushOrder,
            getSummaries(){
                let sums = [];
                sums[1] = '总计';
                sums[9] = this.countData.total_amount;
                sums[10] = this.countData.total_cost;
                sums[11] = this.countData.total_fee;
                sums[12] = this.countData.total_tax_fee;
                sums[13] = this.countData.total_promotion_fee;
                sums[14] = this.countData.total_other_fee;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['refund']);
                }

                let obj = {
                    cbegintime: this.queryData.proofDate[0],
                    cendtime: this.queryData.proofDate[1],
                    billno: this.queryData.billNo,
                    contacts: this.queryData.contacts,
                    k3_bill_no: this.queryData.k3BillNo,
                    sync_status: this.queryData.busType
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'refund',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetRefundOrderInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 重置
            handleResetQueryInfo() {

                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    busType: '0',
                    contacts: '',
                    billNo: '',
                    k3BillNo: ''
                };

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },

            //推送 按钮方法
            handlePush(idx, row) {

                //单行推送
                if (row) {
                    this.isPushShow = true;
                    this.pushData.ids = row.id;
                } else {
                    if (!this.tableSelectCheck.length) return;

                    //多行推送
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选未推送数据
                            if (this.tableSelectCheck[i].sync_status === "0") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }
                        this.pushData.ids = paramsArr.join(',');

                        if(this.pushData.ids) {
                            this.isPushShow = true;
                        }
                    }
                }
            },

            //确认 推送方法
            handleConfirmPush() {

                let obj = {
                    sync_time: this.pushData.proofDate,
                    ids: this.pushData.ids
                };

                this.handlePushOrder(obj).then(({data}) => {

                    if (data.status) {
                        this.$message({
                            type: 'success',
                            message: '推送成功'
                        });
                        this.handleQueryInfo({limit: this.pageData.currentList});
                    } else {
                        this.$message({
                            type: 'error',
                            message: data.errorMess || '推送失败'
                        });
                    }
                }).catch(err => {
                    console.log(err);
                });
                this.isPushShow = false;
            },

            // 取消推送方法
            handleCancelPush() {
                this.isPushShow = false;
                this.pushData.proofDate = '';
                this.pushData.ids = '';
            },

            //删除推送 按钮方法
            handleDeletePush(idx, row) {

                //单行推送删除
                if (row) {
                    this.pushData.ids = row.id;
                } else {
                    if (!this.tableSelectCheck.length) return;

                    //多行推送删除
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选推送数据
                            if (this.tableSelectCheck[i].sync_status === "2") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }
                        this.pushData.ids = paramsArr.join(',');
                    }
                }

                if (!this.pushData.ids) return;

                this.$confirm('执行后将该单据状态变成“未推送”，并且不支持删除K3数据，为了避免误操作，请您再次确定后点击【确定】', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {

                    let obj = {
                        ids: this.pushData.ids
                    };
                    this.delPushOrder(obj).then(({ data }) => {
                        if (data.status) {
                            this.$message({
                                type: 'success',
                                message: '删除推送成功!'
                            });
                            this.handleQueryInfo({limit: this.pageData.currentList});
                        } else {
                            this.$message({
                                type: 'error',
                                message: data.errorMess || '删除推送失败!'
                            });
                        }
                    }).catch(err => {
                        return err;
                    });

                }).catch(() => {
                    console.log('取消删除')
                });
            },

            //控制可否勾选项
            autoCustomerSelect(row) {
                return row.sync_status !== '1' ? 1 : 0;
            },

            //勾选方法
            handleSelectionChange(val) {
                this.tableSelectCheck = val;
                // console.log(this.tableSelectCheck)
            },

            // 分页
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },

            // 分页
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 下载模板
            handleDownloadModule() {
                window.open(this.downloadUrl, '_self');
            },

            // 查询回调
            handleQueryInfoCallBack(data) {
                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                // 表格数据渲染
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.totalOrders = data.total_qty;
                this.countData = data.page_data;

                this.optionList.syncStatusList = data.data_list.sync_status;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.refundOrderInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['refund']);
                        this.handleQueryInfoCallBack(this.refundOrderInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

</style>


