
import * as types from './mutation-type';
import * as Storage from '@/utils/storage';
import buttonList from '../authority/button_config';
import other_in_stock from "./module/storage_management/other_in_stock_order";
import customer_form from "./module/report_forms/customer_form";
import purchase_order from "./module/purchase_management/purchase_order";
import purchase_refund from "./module/cashier_management/purchase_refund";
import inventory_losses from "./module/storage_management/inventory_losses_order";
import payreal from "./module/cashier_management/payable_reality_order";
import purchase_freight from "./module/purchase_management/purchase_freight";
import payeste from "./module/cashier_management/payable_estimate_order";
import receipt from "./module/cashier_management/receipt_order";
import payable_reality_form from "./module/report_forms/payble_reality_form";
import push_error from "./module/error_log/push_error";
import purchase_in_stock from "./module/purchase_management/purchase_in_stock";
import cost_receivable from "./module/report_forms/cost_receivable";
import refund from "./module/cashier_management/refund_order";
import payable_estimate_form from "./module/report_forms/payble_estimate_form";
import inventory_profit from "./module/storage_management/inventory_profit_order";
import sales from "./module/marketing_management/sales_order";
import purchase_return from "./module/purchase_management/purchase_return";
import other_out_stock from "./module/storage_management/other_out_stock_order";
import purchase_payment from "./module/cashier_management/purchase_payment";
import sales_form from "./module/report_forms/sales_form";
import receivables from "./module/cashier_management/receivables_order";
import sales_sum_form from "./module/report_forms/sales_sum_form";
import sales_refund_order from "./module/marketing_management/sales_refund_order";
import purchase_discount from "./module/purchase_management/purchase_discount";
import operate_sum from "./module/operate_management/operate_sum";
import receipt_sum_form from "./module/report_forms/receipt_sum_form";
import sum_error from "./module/error_log/sum_error";

export default {
    [types.SAVE_LOADING_STATE] (state, data) {
        state.showLoading = data;
    },

    // 保存登录信息
    [types.SAVE_LOGIN_INFO] (state, loginInfo) {
        state.loginInfo = loginInfo;
        // Storage.setSessionStorage('userInfo', loginInfo);
    },

    [types.SAVE_BUTTON_CONFIG] (state, saveInfo) {
        state.buttonList = buttonList[saveInfo.replace(/^[\/]/, '')] && buttonList[saveInfo.replace(/^[\/]/, '')].buttonList;
        Storage.setSessionStorage('buttonList', saveInfo);
    },

    // 保存查询参数
    [types.SAVE_QUERY_PARAMS_INFO] (state, saveInfo) {
        state.queryParamsInfo[saveInfo.key] = saveInfo.value;
    },
    [types.SAVE_PERMIT_PATH_INFO] (state, infoList) {
        let arr = [];
        for (let i = 0; i < infoList.length; i++) {
            arr.push(infoList[i].resource_path)
        }
        state.permitPath = arr;
        Storage.setSessionStorage('permitPath', arr);
    },
    [types.SAVE_PERMIT_RESOURCE_INFO] (state, infoList) {
        // 递归函数
        let callBack = function (children, saveObj, isRoot) {
            if (children && children.length) {
                for (let i = 0; i < children.length; i++) {
                    if (children[i].children && children[i].children.length) {
                        saveObj[children[i].resource_name] = {};
                        callBack(children[i].children, saveObj[children[i].resource_name])
                    } else {
                        saveObj[children[i].resource_name] = true
                    }
                }
            } else if (isRoot) {
                return true;
            }
        };
        let obj = {};
        for (let i = 0; i < infoList.length; i++) {
            obj[infoList[i].resource_name] = {};
            callBack(infoList[i].children, obj[infoList[i].resource_name], true) === true ? obj[infoList[i].resource_name] = true : false;
        }
        state.permitResource = obj;
        Storage.setSessionStorage('permitResource', obj);
    },

    /**
     * 保存页签:
     *  isRemove 1 移除单个; 2 移除全部
     * **/
    [types.SAVE_TAB_LABELS] (state, saveInfo) {
        if (saveInfo.isRemove === 1) {
            state.tabLabel.splice(saveInfo.isRemoveInx, 1);
            let resetObj = state[saveInfo.path.replace('/', '')];
            for (let key in resetObj) {
                switch (typeof resetObj[key]) {
                    case 'number':
                        resetObj[key] = 0;
                        break;
                    case 'string':
                        resetObj[key] = '';
                        break;
                    case 'boolean':
                        resetObj[key] = false;
                        break;
                    case 'object':
                        resetObj[key] = resetObj[key] instanceof Array ? [] : {};
                        break;
                }
            }
        } else if (saveInfo.isRemove === 2) {
            let leaveArr = [];
            for (let i = 0; i < state.tabLabel.length; i++) {
                if (state.tabLabel[i].path === saveInfo.path) {
                    leaveArr.push(state.tabLabel[i]);
                    break;
                }
            }
            // 关闭全部的缓存
            let pathArr = ['sales', 'sales_refund_order', 'purchase_order', 'purchase_in_stock', 'purchase_return', 'purchase_discount', 'purchase_freight', 'receivables', 'payreal', 'payeste', 'receipt', 'refund', 'purchase_payment', 'purchase_refund', 'inventory_losses', 'inventory_profit', 'other_in_stock', 'other_out_stock', 'cost_receivable', 'customer_form', 'payable_estimate_form', 'payable_reality_form', 'receipt_sum_form', 'sales_form', 'sales_sum_form', 'operate_sum', 'push_error', 'sum_error', 'operate_abnormal'];
            for (let i = 0; i < pathArr.length; i++) {
                if (pathArr[i] === saveInfo.path.replace('/', '')) continue;
                let resetObj = state[pathArr[i]];
                for (let key in resetObj) {
                    switch (typeof resetObj[key]) {
                        case 'number':
                            resetObj[key] = 0;
                            break;
                        case 'string':
                            resetObj[key] = '';
                            break;
                        case 'boolean':
                            resetObj[key] = false;
                            break;
                        case 'object':
                            resetObj[key] = resetObj[key] instanceof Array ? [] : {}
                            break;
                    }
                }
            }
            state.tabLabel.splice(0);
            state.tabLabel = leaveArr;
        } else {
            for (let i = 0; i < state.tabLabel.length; i++) {
                if (state.tabLabel[i].path === saveInfo.path) {
                    return;
                }
            }
            if (state.tabLabel.length > 7) {
                state.tabLabel.shift();
            }
            state.tabLabel.push(saveInfo);
        }
    },

    // 导航激活INDEX
    [types.SAVE_NAV_INDEX] (state, saveInfo) {
        state.navIndex = saveInfo;
    }
}


