<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model='queryData.pushState'
                                placeholder='请选择'
                                value-key>
                            <el-option
                                    v-for="item in optionList.syncStatusList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>汇总状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.summaryStatus"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.summaryTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.carrier" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.receiptNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.k3No" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    border
                    stripe
                    class="ui-data-height"
                    :summary-method="getSummaries"
                    show-summary
                    style="width: 100%"
                    slot="empty"
                    :data="tableData"
                    :height="tableHeight"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        :selectable="autoCustomerSelect"
                        width="40">
                </el-table-column>
                <el-table-column
                        type="index"
                        width="80"
                        :label="tableKey[0]">
                </el-table-column>
                <el-table-column
                        prop="package_no"
                        width="200"
                        :label="tableKey[1]">
                </el-table-column>
                <el-table-column
                        prop="carrier_id"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="carrier_name"
                        width="120"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        width="150"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="currency_cn"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        :width="tableColumnWidth.date"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="ship_time"
                        :width="tableColumnWidth.date"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        :label="tableKey[9]"
                        width="200"
                        prop="summary_bill_no">
                </el-table-column>
                <el-table-column
                        :label="tableKey[10]"
                        width="180"
                        prop="summary_time">
                </el-table-column>
                <el-table-column :label="tableKey[11]">
                    <template slot-scope="scope">
                        <span :class="'ui-txt-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        :label="tableKey[12]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.summary_status">{{ scope.row.summary_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        :label="tableKey[13]"
                        :width="tableColumnWidth.date"
                        prop="sync_time">
                </el-table-column>
                <el-table-column
                        :label="tableKey[14]"
                        prop="real_name_cn">
                </el-table-column>
                <el-table-column
                        :label="tableKey[15]"
                        :width="tableColumnWidth.date"
                        prop="real_time">
                </el-table-column>
                <el-table-column
                        prop="summary_status"
                        width="120"
                        label="操作">
                    <template slot-scope="scope">
                        <div v-show="isPushConfig">
                            <el-button
                                    v-show="scope.row.sync_status == '0'"
                                    type="primary"
                                    plain
                                    size="medium"
                                    @click="handlePush(scope.$index, scope.row)">推送
                            </el-button>
                            <el-button
                                    v-show="scope.row.sync_status == '2'"
                                    type="primary"
                                    plain
                                    size="medium"
                                    @click="handleDeletePush(scope.$index, scope.row)">删除推送
                            </el-button>
                        </div>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总金额：{{pageData.aggregateAmount}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

        <el-dialog
                center width="450px"
                title="推送单据"
                :visible.sync="isPushShow"
                :close-on-click-modal="false">
            <div>
                <div class="ui-com-center">
                    <span class="ui-com-pr">单据日期</span>
                    <el-date-picker
                            v-model="pushData.proofDate"
                            type='date'
                            value-format='yyyy-MM-dd'
                            placeholder="选择日期">
                    </el-date-picker>
                </div>
                <p class="ui-com-center ui-put-dialog" style="margin-top: 8px;">请你选择一个单据的生成日期</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancelPush()">放 弃</el-button>
                <el-button type="primary" @click="handleConfirmPush()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getPayableRealityInfo,
        handlePushOrder,
        delPushOrder
    } from '@/api/cashier_management/payable_reality_order';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                tableColumnWidth: {
                    date: 180,
                    orderNo: 150
                },
                tableData: [],
                tableKey: ["序号", "单据编号", "承运商id", "承运商名称", "往来单位", "金额", "币别","创建时间", "发货时间", "K3单据号", "汇总时间", "状态","汇总状态", "推送时间", "实际结算人", "实际结算时间","操作"],
                //初始化查询数据
                queryData: {
                    proofDate: [],
                    pushState: '',
                    summaryStatus: '',
                    carrier: '',
                    receiptNo: '',
                    k3No: ''
                },
                optionList: {
                    syncStatusList: [{
                        value: '',
                        label: '全部'
                    }, {
                        value: '0',
                        label: '未推送'
                    }, {
                        value: '1',
                        label: '已推送'
                    }, {
                        value: '2',
                        label: '推送中'
                    }],
                    summaryTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未汇总"},
                        {value: "1", label: "已汇总"},
                        {value: "2", label: "汇总失败"}
                    ]
                },
                saveDefaultDate: [],

                isPushShow: false,
                pushData: {
                    proofDate: ''
                },
                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },
                isPushConfig: true,
                delPushParams: [],
                pushParams: {}
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'payableRealityInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetPayableRealityInfo'
            ]),
            getDefaultTime,
            autoAddTableInx,
            getPayableRealityInfo,
            handlePushOrder,
            delPushOrder,
            getSummaries(){
                let sums = [];
                sums[1] = '总计';
                sums[6] = this.countData.total_amount;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['payable_reality']);
                }

                let obj = {
                    bill_start_time: this.queryData.proofDate[0],
                    bill_end_time: this.queryData.proofDate[1],
                    bill_no: this.queryData.receiptNo,
                    carrier_id: this.queryData.carrier,
                    sync_status: this.queryData.pushState,
                    summary_bill_no: this.queryData.k3No,
                    summary_status: this.queryData.summaryStatus

                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'payable_reality',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetPayableRealityInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                })
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    summaryStatus: '',
                    pushState: '',
                    carrier: '',
                    receiptNo: '',
                    k3No: ''
                }
            },

            // 分页
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },

            // 分页
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            //触发勾选框事件
            handleSelectionChange(val) {
                this.tableSelectCheck = val
            },

            //推送 按钮方法
            handlePush(idx, row) {
                //单行推送
                if (row) {
                    this.pushData.ids = row.id;
                    this.isPushShow = true;
                } else {
                    if (!this.tableSelectCheck.length) return;
                    //多行推送
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选未推送数据
                            if (this.tableSelectCheck[i].sync_status === "0") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }

                        if (!paramsArr.length) return;
                        this.isPushShow = true;
                        this.pushData.ids = paramsArr.join(',');
                    }
                }
            },

            //确认 推送方法
            handleConfirmPush() {

                let obj = {
                    ids: this.pushData.ids,
                    sync_time: this.pushData.proofDate
                };

                this.handlePushOrder(obj).then(({data}) => {

                    if (data.status) {
                        this.$message({
                            type: 'success',
                            message: '推送成功'
                        });
                        // this.hintDialogData.pushSuccess = true;
                        this.handleQueryInfo({limit: this.pageData.currentList});
                    } else {
                        // this.hintDialogData.pushFail = true;
                        this.$message({
                            type: 'error',
                            message: data.errorMess || '推送失败'
                        });
                    }
                }).catch(err => {
                    console.log(err);
                });
                this.isPushShow = false;
            },

            // 取消推送方法
            handleCancelPush() {
                this.isPushShow = false;
                this.pushData.proofDate = '';
            },

            //删除推送 按钮方法
            handleDeletePush(idx, row) {

                //单行推送删除
                if (row) {
                    this.delPushParams.ids = row.id;
                } else {
                    if (!this.tableSelectCheck.length) return;

                    //多行推送删除
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选推送数据
                            if (this.tableSelectCheck[i].sync_status === "2") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }
                        this.delPushParams.ids = paramsArr.join(',');
                    }
                }

                if (!this.delPushParams.ids) return;

                this.$confirm('执行后将该单据状态变成“未推送”，并且不支持删除K3数据，为了避免误操作，请您再次确定后点击【确定】', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {
                    this.delPushOrder(this.delPushParams).then(({ data }) => {
                        if (data.status) {
                            this.$message({
                                type: 'success',
                                message: '删除推送成功!'
                            });
                            this.handleQueryInfo({limit: this.pageData.currentList});
                        } else {
                            this.$message({
                                type: 'error',
                                message: data.errorMess || '删除推送失败!'
                            });
                        }
                    }).catch(err => {
                        return err;
                    });

                }).catch(() => {
                    console.log('取消删除')
                });
            },

            //控制可否勾选项
            autoCustomerSelect(row, idx) {
                return row.sync_status !== '1' ? 1 : 0;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.countData = data;

                if (data.drop_down_box.push_status) {
                    this.optionList.syncStatusList = data.drop_down_box.push_status;
                    this.optionList.summaryTypeList = data.drop_down_box.summary_statu;
                }

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.payableRealityInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['payable_reality']);
                        this.handleQueryInfoCallBack(this.payableRealityInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>
<style lang="less" scoped>

</style>



