<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model='queryData.statusType'
                                placeholder='请选择'
                                value-key>
                            <el-option v-for='item in optionList.syncStatusList' :key='item.value' :label='item.label'
                                       :value='item.value'>
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>汇总状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.summaryStatus"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.summaryTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.contactUnit" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.k3DocNo" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    :data="tableData"
                    class="ui-data-height"
                    border
                    stripe
                    :summary-method="getSummaries"
                    show-summary
                    style="width: 100%"
                    slot="empty"
                    :height="tableHeight">
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        width="80"
                        :index="autoAddTableInx">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[1]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        width="150"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="currency"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        width="180"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="ship_time"
                        width="180"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="summary_bill_no"
                        width="200"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="summary_time"
                        width="180"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        :label="tableKey[9]">
                    <template slot-scope="scope">
                        <span :class="'ui-txt-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        :label="tableKey[10]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.summary_status">{{ scope.row.summary_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="sync_time"
                        width="180"
                        :label="tableKey[11]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getReceivablesOrderInfo
    } from '@/api/cashier_management/receivables_order';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                tableColumnWidth: {
                    date: 100,
                    orderNo: 150
                },

                //初始化查询数据
                queryData: {
                    proofDate: [],
                    statusType: '0',
                    summaryStatus: '',
                    contactUnit: '',
                    docNo: '',
                    k3DocNo: ''
                },

                optionList: {
                    syncStatusList: [{
                        value: '',
                        label: '全部'
                    }, {
                        value: '0',
                        label: '未推送'
                    }, {
                        value: '1',
                        label: '推送中'
                    }, {
                        value: '2',
                        label: '已推送'
                    }],
                    summaryTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未汇总"},
                        {value: "1", label: "已汇总"},
                        {value: "2", label: "汇总失败"}
                    ]
                },

                // 保存默认时间
                saveDefaultDate: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                //其他数据
                tableData: [],
                tableKey: ["序号", "单据编号", "往来单位", "币别", "金额", "创建时间", "发货时间", "K3单据号","汇总状态", "状态","汇总时间",  "推送时间"],
                countData:{},
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'receivablesInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetReceivablesOrderInfo'
            ]),
            getReceivablesOrderInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries(){
                let sums = [];
                sums[0] = '总计';
                sums[4] = this.countData.total_amount;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['receivables']);
                }

                let obj = {
                    bill_start_time: this.queryData.proofDate[0],
                    bill_end_time: this.queryData.proofDate[1],
                    bill_no: this.queryData.docNo,
                    customer: this.queryData.contactUnit,
                    sync_status: this.queryData.statusType,
                    summary_bill_no: this.queryData.k3DocNo,
                    summary_status: this.queryData.summaryStatus
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'receivables',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetReceivablesOrderInfo(obj).then(({ data }) => {

                    // 导出
                    if(data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err)
                });
            },
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    statusType: '0',
                    summaryStatus: '',
                    contactUnit: '',
                    docNo: '',
                    k3DocNo: ''
                };
            },
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data.value.length;

                this.tableData = data.data.value;
                this.tableKey = data.data.key;
                this.countData = data.page;
                this.optionList.syncStatusList = data.data.sync_status;

                //获取分页信息
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.receivablesInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['receivables']);
                        this.handleQueryInfoCallBack(this.receivablesInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

</style>


