
/**  --->>>>   |^ 应付单（ 差额 ）^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取应付单信息
export function getPayableDifferenceInfo(queryData) {
    const data = queryData.limit ? Object.assign(queryData, CONFIG.ajaxData) : Object.assign(queryData, CONFIG.ajaxData, {
        limit: 20,
        offset: 1
    });

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/pay/diff_pay/getactualpayablediff' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/pay/diff_pay/getactualpayablediff', data);
    }
}


