<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>放款时间</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>往来单位</span>
                        <el-input v-model="queryData.customer" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>币别</span>
                        <el-select
                                v-model="queryData.coinType"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.currencyList"
                                    :key="item.key"
                                    :label="item.value"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>收支类型</span>
                        <el-select
                                v-model="queryData.inOutType"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.inOutTypeList"
                                    :key="item.key"
                                    :label="item.value"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>处理状态</span>
                        <el-select
                                v-model="queryData.disposeState"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.disposeStateList"
                                    :key="item.key"
                                    :label="item.value"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <ComTableInfo
                isCheck
                :selectable="autoCustomerSelect"
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :showSearch="showSearch"
                @checked="handleSelectionChange">
            <template slot="operation" slot-scope="scope">
                <el-button
                        type="primary"
                        size="medium"
                        :disabled="buttonList[1].permit(thisVm) || scope.row.sign_status === '2'"
                        plain
                        @click="handleFlagDispose(scope.row)">标记处理
                </el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx,
        changeTableInfoProps
    } from '@/services/index'
    import {
        flagDisposeInfo
    } from '@/api/report_forms/expenses_form';

    export default {
        data() {
            return {

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                optionList: {
                    inOutTypeList:[],
                    currencyList: [],
                    disposeStateList: []
                },

                // 查询条件
                queryData: {
                    proofDate: [],
                    customer: '',
                    coinType: '',
                    inOutType: '',
                    disposeState: '',
                },

                // 保存默认时间
                saveDefaultDate:[],

                tableKey:[],
                tableData: [],
                tableProps: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0
                },

                // 保存勾选数据
                saveCheckData: []
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'expensesFormInfo',
                'loginInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetExpensesFormInfo'
            ]),
            flagDisposeInfo,
            getDefaultTime,
            autoAddTableInx,
            changeTableInfoProps,
            getSummaries() {
                let sums = [];
                sums[0] = '总计';
                sums[7] = this.countData.total_qty;
                // sums[9] = this.countData.total_price;
                sums[10] = this.countData.total_amount;
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    customer: '',
                    coinType: '',
                    inOutType: '',
                    disposeState: '',
                }
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['expenses_form']);
                }

                let obj = {
                    start_time: this.queryData.proofDate[0],
                    end_time: this.queryData.proofDate[1],
                    contacts: this.queryData.customer,
                    currency: this.queryData.coinType,
                    cost_order_type: this.queryData.inOutType,
                    sign_status: this.queryData.disposeState,
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'expenses_form',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetExpensesFormInfo(obj).then(({ data }) => {
                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 分页
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },

            // 分页
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            //控制可否勾选项
            autoCustomerSelect(row) {
                return row.sign_status === '1';
            },

            // 勾选表格数据
            handleSelectionChange(row) {
                this.saveCheckData = row;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1,
                    total: this.pageData.currentTotal
                }, true);
            },

            // 标记处理
            handleFlagDispose(row) {
                if (row) {
                    this.$confirm('已处理的数据不会重复处理。', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消'
                    }).then(() => {
                        let obj = {
                            ids: JSON.stringify([row.id]),
                            operator: this.loginInfo.uid
                        };

                        this.flagDisposeInfo(obj).then(({ data }) => {
                            this.handleOperationCallBack(data);
                        });
                    })
                } else {
                    let arr = [];
                    for (let i = 0; i < this.saveCheckData.length; i++) {
                        if (this.saveCheckData[i].sign_status === 2) continue;
                        arr.push(this.saveCheckData[i].id)
                    }
                    if (!arr.length) return;

                    this.$confirm('已处理的数据不会重复处理。', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消'
                    }).then(() => {
                        let obj = {
                            ids: JSON.stringify(arr),
                            operator: this.loginInfo.uid
                        };

                        this.flagDisposeInfo(obj).then(({ data }) => {
                            this.handleOperationCallBack(data);
                        });
                    })
                }
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo();
                } else {
                    this.$message.error(data.errorMess || '操作失败')
                }
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                //渲染数据
                this.optionList.currencyList = data.data_list.currency_list;
                this.optionList.disposeStateList = data.data_list.handle_status_list;
                this.optionList.inOutTypeList = data.data_list.cost_order_type;

                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;

                this.changeTableInfoProps(this.tableData, 'sign_status', 'sign_status_cn', this.optionList.disposeStateList);
                this.changeTableInfoProps(this.tableData, 'order_type', 'order_type_cn', this.optionList.inOutTypeList);

                this.tableProps = ['index', 'customer', 'currency_name', 'price', 'order_type_cn', 'sign_status_cn', 'loan_time', 'operation'];

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.expensesFormInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['expenses_form']);
                        this.handleQueryInfoCallBack(this.expensesFormInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        }
    };
</script>

<style lang="less" scoped>
</style>


