<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.businessDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.statusType"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.pushTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>k3单据号</span>
                        <el-input v-model="queryData.k3BillNo" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.contactUnit" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.purchaseOrderNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>采购员</span>
                        <el-input v-model="queryData.buyer" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    class="ui-data-height"
                    border
                    stripe
                    :summary-method="getSummaries"
                    show-summary
                    style="width: 100%"
                    :height="tableHeight">
                <el-table-column
                        type="selection"
                        width="40">
                </el-table-column>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        :label="tableKey[1]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        :label="tableKey[2]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="dates"
                        :label="tableKey[3]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="purchase_no"
                        :label="tableKey[4]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="provider_id"
                        width="150"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="purchase_id"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="sku"
                        width="150"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="price"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="retreating_qty"
                        :label="tableKey[9]">
                </el-table-column>
                <el-table-column
                        prop="warehouse_code"
                        width="150"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        prop="sync_date"
                        width="180"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        width="100"
                        :label="tableKey[12]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总数量：{{pageData.totalQuantity}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getPurchaseReturnInfo,
        handlePushOrder,
        delPushOrder
    } from '@/api/purchase_management/purchase_return';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,

                // 查询下拉列表
                optionList: {
                    pushTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未推送"},
                        {value: "1", label: "推送中"},
                        {value: "2", label: "已推送"}
                    ],
                    packTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "否"},
                        {value: "1", label: "是"},
                    ]
                },

                // 查询参数
                queryData: {
                    businessDate: [],
                    statusType: '',
                    contactUnit: '',
                    k3BillNo: '',
                    buyer: '',
                    purchaseOrderNo: ''
                },

                //设置默认时间
                saveDefaultDate: [],

                // 列表数据
                tableKey: [],
                tableData: [],

                //总订单合计
                totalOrders: '',
                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'purchaseReturnInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetPurchaseReturnInfo'
            ]),
            getPurchaseReturnInfo,
            handlePushOrder,
            getDefaultTime,
            autoAddTableInx,
            delPushOrder,
            getSummaries(){
                let sums = [];
                sums[1] = '总计';
                sums[10] = this.countData.total_qty;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['purchase_return']);
                }

                let obj = {
                    cbegintime: this.queryData.businessDate[0],
                    cendtime: this.queryData.businessDate[1],
                    k3_bill_no: this.queryData.k3BillNo,
                    supplier_id: this.queryData.contactUnit,
                    sync_status: this.queryData.statusType,
                    purchase_id: this.queryData.buyer,
                    purchase_order_no: this.queryData.purchaseOrderNo,
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'purchase_return',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetPurchaseReturnInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 重置
            handleResetQueryInfo() {

                this.queryData = {
                    businessDate: this.saveDefaultDate,
                    statusType: '',
                    contactUnit: '',
                    k3BillNo: '',
                    buyer: '',
                    purchaseOrderNo: ''
                };

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                // 表格数据渲染
                this.countData = data.page_data;
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.totalOrders = data.total_qty;

                this.optionList.pushTypeList = data.data_list.sync_status;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.purchaseReturnInfo) === '{}') {
                        this.queryData.businessDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['purchase_return']);
                        this.handleQueryInfoCallBack(this.purchaseReturnInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
</style>


