<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>同步状态</span>
                        <el-select
                                v-model="queryData.syncStatus"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.syncStatusList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>收款账号</span>
                        <el-input v-model="queryData.bankAccount" placeholder="请输入" size="medium"></el-input>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>往来单位</span>
                        <el-input v-model="queryData.customer" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-main-module-table" v-if="initTable">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-table
                    border
                    stripe
                    ref="multipleSelection"
                    :data="tableData"
                    :height="tableHeight"
                    :summary-method="getSummaries"
                    show-summary>
                <el-table-column
                        type="index"
                        label="序号"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        :label="tableKey[0]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        :label="tableKey[1]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        width="150"
                        :label="tableKey[2]">
                </el-table-column>
                <el-table-column
                        prop="currency"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="bank_account"
                        :label="tableKey[4]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[5]">
                </el-table-column>
                <el-table-column
                        prop="fee"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="cost"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="tax_fee"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="promotion_fee"
                        :label="tableKey[9]">
                </el-table-column>
                <el-table-column
                        prop="other_fee"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        prop="order_price"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        prop="sync_status_cn"
                        :label="tableKey[12]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        width="180"
                        prop="sync_time"
                        :label="tableKey[13]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <!--<span class="ui-tip-txt">总金额:{{pageData.aggregateAmount}}</span>-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getReceiptSumFormInfo
    } from '@/api/report_forms/receipt_sum_form';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                optionList: {
                    packType:[
                        {value:'',label:"全部"},
                        {value:"0",label:"否"},
                        {value:"1",label:"是"},
                    ],
                    syncStatusList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未同步"},
                        {value: "1", label: "同步中"},
                        {value: "2", label: "同步成功"}
                    ],
                    currencyList: []
                },
                queryData: {
                    proofDate: [],
                    syncStatus: '',
                    bankAccount: '',
                    customer: '',
                    docNo: ''
                },
                saveDefaultDate:[],

                tableKey:["业务日期", "单据编号", "虚拟包裹", "单据编号", "往来单位", "SKU", "出库数量", "币别", "单价", "金额", "K3单据号", "推送日期"],
                tableData: [],

                //总金额
                totalPrice:'',

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0
                },

                // 订单号
                order_no: '',
                countData: {},
                coinTypeList: {}
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'receiptSumFormInfo'
            ])
        },
        methods: {
            ...mapActions([
               'GetReceiptSumFormInfo'
            ]),
            getReceiptSumFormInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries() {
                let sums = [];
                sums[0] = '总计';
                sums[6] = this.countData.total_amount;
                sums[7] = this.countData.total_fee;
                sums[8] = this.countData.total_cost;
                sums[9] = this.countData.total_tax_fee;
                sums[10] = this.countData.total_promotion_fee;
                sums[11] = this.countData.total_other_fee;
                sums[12] = this.countData.total_order_price;
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    syncStatus: '',
                    bankAccount: '',
                    customer: '',
                    docNo: ''
                }
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['receipt_sum_form']);
                }

                let obj = {
                    cbegintime: this.queryData.proofDate[0],
                    cendtime: this.queryData.proofDate[1],
                    bill_no: this.queryData.docNo,
                    status: this.queryData.syncStatus,
                    customer: this.queryData.customer,
                    bank_account: this.queryData.bankAccount,
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'receipt_sum_form',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetReceiptSumFormInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1,
                    total: this.pageData.currentTotal
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                this.optionList.syncStatusList = data.data_list.sync_status_data;
                this.optionList.currencyList = data.data_list.currency;
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.countData = data.page_data;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.receiptSumFormInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['receipt_sum_form']);
                        this.handleQueryInfoCallBack(this.receiptSumFormInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>
</style>


