<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.proofDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>虚拟包裹</span>
                        <el-select
                                v-model="queryData.packStatus"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.packType"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>订单号</span>
                        <el-input v-model="queryData.orderNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.customer" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.searchProof" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.searchK3Proof" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>币别</span>
                        <el-select
                                v-model="queryData.coinType"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.currencyList"
                                    :key="item"
                                    :label="item"
                                    :value="item">
                            </el-option>
                        </el-select>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo()" >重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    border
                    stripe
                    class="ui-data-height"
                    :summary-method="getSummaries"
                    show-summary
                    style="width: 100%"
                    :height="tableHeight">
                <el-table-column
                        type="index"
                        width="80"
                        :index="autoAddTableInx"
                        :label="tableKey[0]">
                </el-table-column>
                <el-table-column
                        prop="ship_date"
                        :label="tableKey[1]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="package_no"
                        :label="tableKey[2]"
                        width="220">
                </el-table-column>
                <el-table-column
                        prop="is_virtual"
                        :label="tableKey[3]"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[4]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        :label="tableKey[5]"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="sku"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="qty"
                        :label="tableKey[7]">
                </el-table-column>
                <el-table-column
                        prop="currency"
                        :label="tableKey[8]">
                </el-table-column>
                <el-table-column
                        prop="price"
                        :label="tableKey[9]" >
                </el-table-column>
                <el-table-column
                        prop="amount"
                        :label="tableKey[10]">
                </el-table-column>
                <el-table-column
                        width="200"
                        prop="summary_bill_no"
                        :label="tableKey[11]">
                </el-table-column>
                <el-table-column
                        width="180"
                        prop="sync_time"
                        :label="tableKey[12]">
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import config from '@/assets/js/config';
    import {
        getDefaultTime,
        autoAddTableInx
    } from '@/services/index'
    import {
        getSalesFormInfo
    } from '@/api/report_forms/sales_form';

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,
                optionList: {
                    packType:[
                        {value:'',label:"全部"},
                        {value:"0",label:"否"},
                        {value:"1",label:"是"},
                    ],
                    currencyList: []
                },
                queryData: {
                    proofDate: [],
                    packStatus: '',
                    coinType: '',
                    orderNo: '',
                    customer: '',
                    searchProof: '',
                    searchK3Proof: ''
                },
                saveDefaultDate:[],

                tableKey:["业务日期", "单据编号", "虚拟包裹", "单据编号", "往来单位", "SKU", "出库数量", "币别", "单价", "金额", "K3单据号", "推送日期"],
                tableData: [],

                //总金额
                totalPrice:'',

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0
                },

                // 订单号
                order_no: '',
                countData: {},
                coinTypeList: {}
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'salesFormInfo'
            ])
        },
        methods: {
            ...mapActions([
               'GetSalesFormInfo'
            ]),
            getSalesFormInfo,
            getDefaultTime,
            autoAddTableInx,
            getSummaries() {
                let sums = [];
                sums[0] = '总计';
                sums[7] = this.countData.total_qty;
                // sums[9] = this.countData.total_price;
                sums[10] = this.countData.total_amount;
                return sums;
            },

            // 重置
            handleResetQueryInfo() {
                this.queryData = {
                    proofDate: this.saveDefaultDate,
                    packStatus: '',
                    coinType: '',
                    orderNo: '',
                    customer: '',
                    searchProof: '',
                    searchK3Proof: ''
                }
            },

            // 查询
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sales_form']);
                }

                let obj = {
                    order_id: this.queryData.orderNo,
                    cbegintime: this.queryData.proofDate[0],
                    cendtime: this.queryData.proofDate[1],
                    bill_no: this.queryData.searchProof,
                    summary_bill_no: this.queryData.searchK3Proof,
                    is_virtual_package: this.queryData.packStatus,
                    customer: this.queryData.customer,
                    currency: this.queryData.coinType,
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'sales_form',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetSalesFormInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true);
            },
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1,
                    total: this.pageData.currentTotal
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                //渲染数据
                this.countData = data;
                this.optionList.currencyList = data.data_list.currency;
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.totalPrice = data.total_price;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit * 1 === 0 ? 20 : data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.salesFormInfo) === '{}') {
                        this.queryData.proofDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['sales_form']);
                        this.handleQueryInfoCallBack(this.salesFormInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>
</style>


