<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>业务日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.businessDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>推送状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.statusType"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.pushTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>汇总状态</span>
                        <el-select
                                size="medium"
                                v-model="queryData.summaryStatus"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.summaryTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>虚拟包裹</span>
                        <el-select
                                v-model="queryData.packStatus"
                                placeholder="请选择"
                                size="medium">
                            <el-option
                                    v-for="item in optionList.packTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="ui-main-module-search clearFix">
                    <li class="ui-align-layout">
                        <span>往来单位</span>
                        <el-input v-model="queryData.contactUnit" placeholder="可输入关键字进行检索" size="medium"></el-input>
                    </li>
                    <li>
                        <span>订单号</span>
                        <el-input v-model="queryData.orderNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.docNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li>
                        <span>K3单据号</span>
                        <el-input v-model="queryData.k3DocNo" placeholder="多条单据请用‘,’分隔检索" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button size="medium" @click="handleResetQueryInfo">重置</el-button>
                        <el-button size="medium" @click="handleQueryInfo({limit: pageData.currentList})" type="primary">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    stripe
                    border
                    show-summary
                    ref="multipleSelection"
                    style="width: 100%"
                    :data="tableData"
                    :height="tableHeight"
                    :summary-method="getSummaries"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        :selectable="autoCustomerSelect"
                        width="40">
                </el-table-column>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_type"
                        :label="tableKey[1]"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="order_id"
                        :label="tableKey[2]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="is_virtual"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="ship_date"
                        :label="tableKey[4]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="package_no"
                        :label="tableKey[5]"
                        width="220">
                </el-table-column>
                <el-table-column
                        prop="total_qty"
                        :label="tableKey[6]">
                </el-table-column>
                <el-table-column
                        prop="customer"
                        :label="tableKey[7]"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="summary_bill_no"
                        :label="tableKey[8]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="sync_time"
                        :label="tableKey[9]"
                         width="180">
                </el-table-column>
                <el-table-column
                        width="100"
                        :label="tableKey[10]">
                    <template slot-scope="scope">
                        <span :class="'ui-txt-color' + scope.row.sync_status">{{ scope.row.sync_status_cn }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        width="100"
                        :label="tableKey[11]">
                    <template slot-scope="scope">
                        <span :class="'ui-state-color' + scope.row.summary_status"  v-show="scope.row.is_virtual === '否' ">
                            {{ scope.row.summary_status_cn }}
                        </span>
                        <span  v-show="scope.row.is_virtual === '是' "> {{ "——" }} </span>
                    </template>
                </el-table-column>
                <!-- 推送操作 -->
                <el-table-column prop="operate" label="操作" width="120">
                    <template slot-scope="scope">
                        <el-button
                                type="primary"
                                v-show="scope.row.sync_status === '0' && scope.row.is_virtual === '是' "
                                plain
                                :disabled="buttonList[0].permit(thisVm)"
                                @click="handlePush(scope.$index, scope.row)">推送
                        </el-button>
                        <el-button
                                type="primary"
                                v-show="scope.row.sync_status === '2' && scope.row.is_virtual === '是' "
                                plain
                                :disabled="buttonList[1].permit(thisVm)"
                                @click="handleDeletePush(scope.$index, scope.row)">删除推送
                        </el-button>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog
                center width="450px"
                title="推送单据"
                :visible.sync="isPushShow"
                :close-on-click-modal="false">
            <div>
                <div class="ui-com-center">
                    <span class="ui-com-pr">单据日期</span>
                    <el-date-picker
                            v-model="pushData.proofDate"
                            type='date'
                            value-format='yyyy-MM-dd'
                            placeholder="选择日期">
                    </el-date-picker>
                </div>
                <p class="ui-com-center ui-put-dialog" style="margin-top: 8px;">请你选择一个单据的生成日期</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancelPush()">放 弃</el-button>
                <el-button type="primary" @click="handleConfirmPush()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        getSalesOrderInfo,
        handlePushOrder,
        delPushOrder
    } from '@/api/marketing_management/sales_order';
    import {
        getDefaultTime,
        autoAddTableInx,
        autoButtonAuthorityControl
    } from '@/services/index';
    import {
        mapGetters,
        mapActions
    } from 'vuex'

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 初始化
                showSearch: true,

                // 是表格动态刷新高度
                initTable: false,

                // 表格默认高度
                tableHeight: 100,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                // 保存实例更新状态
                isCompleted: false,

                // 查询下拉列表
                optionList: {
                    pushTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未推送"},
                        {value: "1", label: "推送中"},
                        {value: "2", label: "已推送"},
                        {value: "3", label: "推送失败"}
                    ],
                    summaryTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "未汇总"},
                        {value: "1", label: "已汇总"},
                        {value: "2", label: "汇总失败"}
                    ],
                    packTypeList: [
                        {value: '', label: "全部"},
                        {value: "0", label: "否"},
                        {value: "1", label: "是"},
                    ]
                },

                // 查询参数
                queryData: {
                    businessDate: [],
                    summaryStatus: '',
                    statusType: '0',
                    packStatus: '',
                    contactUnit: '',
                    docNo: '',
                    k3DocNo: '',
                    orderNo: ''
                },

                //多选数组
                tableSelectCheck: [],

                // 推送状态
                isPushShow: false,
                isDelPushShow: false,

                //推送数据
                pushData: {
                    proofDate: ''
                },

                //删除推送数据
                delPushDialog: {
                    delPush: false
                },

                //提示数据
                hintDialogData: {
                    delFail: false,
                    pushProofing: false,
                    pushSuccess: false,
                    pushFail: false,
                    saveSuccess: false,
                    delPush: false,
                    delSuccess: false,
                    pushNum: 1
                },

                //删除推送数据
                delPushParams: {
                    request_type: 1
                },

                //推送数据
                pushParams: {
                    request_type: 1
                },

                //设置默认时间
                saveDefaultDate: [],

                // 列表数据
                tableKey: [],
                tableData: [],


                //总订单合计
                totalOrders: '',
                countData:{},

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'salesOrderInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSalesOrderInfo'
            ]),
            autoButtonAuthorityControl,
            getSalesOrderInfo,
            handlePushOrder,
            getDefaultTime,
            autoAddTableInx,
            delPushOrder,
            getSummaries(){
                let sums = [];
                sums[1] = '总计';
                sums[7] = this.countData.total_qty;
                return sums;
            },
            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sales']);
                }

                let obj = {
                    cbegintime: this.queryData.businessDate[0],
                    cendtime: this.queryData.businessDate[1],
                    bill_no: this.queryData.docNo,
                    contacts: this.queryData.contactUnit,
                    status: this.queryData.statusType,
                    summary_status: this.queryData.summaryStatus,
                    summary_bill_no: this.queryData.k3DocNo,
                    virtual_package: this.queryData.packStatus,
                    order_id: this.queryData.orderNo
                };

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'sales',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(obj || {}, params);

                this.GetSalesOrderInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            //推送 按钮方法
            handlePush(idx, row) {

                //单行推送
                if (row && row.package_no) {
                    this.isPushShow = true;
                    this.pushParams.ids = row.id;
                } else {
                    //多行推送
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选未推送数据
                            if (this.tableSelectCheck[i].sync_status === "0") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }
                        this.pushParams.ids = paramsArr.join(',');
                        if (this.pushParams.ids) {
                            this.isPushShow = true;
                        }
                    }
                }
            },

            //删除推送 按钮方法
            handleDeletePush(idx, row) {

                //单行推送删除
                if (row && row.package_no) {
                    this.delPushParams.ids = row.id;
                } else {
                    if (!this.tableSelectCheck.length) return;

                    //多行推送删除
                    if (this.tableSelectCheck.length) {
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for (let i = 0; i < len; i++) {

                            //筛选推送数据
                            if (this.tableSelectCheck[i].sync_status === "2") {
                                paramsArr.push(this.tableSelectCheck[i].id);
                            }
                        }
                        this.delPushParams.ids = paramsArr.join(',');
                    }
                }

                if (!this.delPushParams.ids) return;

                this.$confirm('执行后将该单据状态变成“未推送”，并且不支持删除K3数据，为了避免误操作，请您再次确定后点击【确定】', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {

                    let obj = {
                        ids: this.delPushParams.ids
                    };

                    this.delPushOrder(obj).then(({ data }) => {
                        if (data.result && data.status) {
                            this.$message({
                                type: 'success',
                                message: '删除推送成功!'
                            });
                            this.handleQueryInfo({limit: this.pageData.currentList});
                        } else {
                            this.$message({
                                type: 'error',
                                message: data.errorMess || '删除推送失败!'
                            });
                        }
                    }).catch(err => {
                        console.log(err);
                        return err;
                    });

                }).catch(() => {
                    console.log('取消删除')
                });
            },

            //确认 推送方法
            handleConfirmPush(data) {

                if (data) {
                    this.pushParams.sync_time = data;
                } else {
                    this.pushParams.sync_time = '';
                }

                this.handlePushOrder(this.pushParams).then(({data}) => {

                    if (data.result && data.status) {
                        this.$message({
                            type: 'success',
                            message: '推送成功'
                        });
                        this.handleQueryInfo({limit: this.pageData.currentList});
                    } else {
                        this.$message({
                            type: 'error',
                            message: data.errorMess || '推送失败'
                        });
                    }
                }).catch(err => {
                    console.log(err);
                });
                this.isPushShow = false;
            },

            // 取消推送方法
            handleCancelPush() {
                this.isPushShow = false;
                this.pushData.proofDate = '';
            },

            //控制可否勾选项
            autoCustomerSelect(row) {
                return row.is_virtual === '是' && row.sync_status !== '1' ? 1 : 0;
            },

            //勾选方法
            handleSelectionChange(val) {
                this.tableSelectCheck = val;
            },

            // 重置
            handleResetQueryInfo() {

                this.queryData = {
                    businessDate: this.saveDefaultDate,
                    summaryStatus: '',
                    statusType: '0',
                    packStatus: '',
                    contactUnit: '',
                    docNo: '',
                    k3DocNo: '',
                    orderNo: ''
                };

                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },

            // 分页
            handleSizeChange(size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },

            // 分页
            handleCurrentChange(idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 查询回调
            handleQueryInfoCallBack(data) {

                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                // 表格数据渲染
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                this.totalOrders = data.total_qty;
                this.countData = data;

                this.optionList.pushTypeList = data.select_list.push_status;
                this.optionList.summaryTypeList = data.select_list.summary_statu;

                !this.tableData.length ? this.noData = true : this.noData = false;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created() {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.salesOrderInfo) === '{}') {
                        this.queryData.businessDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['sales']);
                        this.handleQueryInfoCallBack(this.salesOrderInfo)
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted() {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
            this.isCompleted = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
</style>


