<template>
    <div class="ui-nav-bar">
        <div class="ui-system-info">
            <!--<span v-show="!isCollapse">汇总系统</span>-->
            <span @click="isCollapse = !isCollapse" :style="{ marginRight: isCollapse ? '18px' : '0' }"><ComIcon name="icon1"></ComIcon></span>
        </div>
        <el-menu
                ref="navMenu"
                class="ui-menu"
                mode="vertical"
                unique-opened
                :default-active="activeIndex"
                :collapse="isCollapse"
                @select="handleSelectModule">
            <el-submenu index="1">
                <template slot="title">
                    <ComIcon :name="!!/^1/.exec(navIndex) ? 'icon5_1' : 'icon5'"></ComIcon>
                    <span :class="!!/^1/.exec(navIndex) ? 'active' : 'default'">销售管理</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="1-1">销售出库订单 <i :class=" navIndex === '1-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="1-2">销售退货订单 <i :class=" navIndex === '1-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title">
                    <ComIcon :name="!!/^2/.exec(navIndex) ? 'icon6_1' : 'icon6'"></ComIcon>
                    <span :class="!!/^2/.exec(navIndex) ? 'active' : 'default'">采购管理</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="2-1">采购订单 <i :class=" navIndex === '2-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-2">采购入库 <i :class=" navIndex === '2-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-3">采购退料 <i :class=" navIndex === '2-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-4">采购折扣 <i :class=" navIndex === '2-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="2-5">采购运费 <i :class=" navIndex === '2-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
                <template slot="title">
                    <ComIcon :name="!!/^3/.exec(navIndex) ? 'icon4_1' : 'icon4'"></ComIcon>
                    <span :class="!!/^3/.exec(navIndex) ? 'active' : 'default'">出纳管理</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="3-1">应收单 <i :class=" navIndex === '3-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-submenu index="3-2">
                        <template slot="title">应付单</template>
                        <el-menu-item index="3-2-1">应付单（实际）<i :class=" navIndex === '3-2-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                        <el-menu-item index="3-2-2">应付单（预估）<i :class=" navIndex === '3-2-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                    </el-submenu>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-3">收款单 <i :class=" navIndex === '3-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-4">收款退款单 <i :class=" navIndex === '3-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-5">采购付款单 <i :class=" navIndex === '3-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="3-6">采购退款单 <i :class=" navIndex === '3-6' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
                <template slot="title">
                    <ComIcon :name="!!/^4/.exec(navIndex) ? 'icon7_1' : 'icon7'"></ComIcon>
                    <span :class="!!/^4/.exec(navIndex) ? 'active' : 'default'">仓储管理</span>
                </template>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="4-1">直接调拨单</el-menu-item>-->
                <!--</el-menu-item-group>-->
                <el-menu-item-group>
                    <el-menu-item index="4-2">盘盈单 <i :class=" navIndex === '4-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-3">盘亏单 <i :class=" navIndex === '4-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-4">其他入库单 <i :class=" navIndex === '4-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="4-5">其他出库单 <i :class=" navIndex === '4-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5">
                <template slot="title">
                    <ComIcon :name="!!/^5/.exec(navIndex) ? 'icon8_1' : 'icon8'"></ComIcon>
                    <span :class="!!/^5/.exec(navIndex) ? 'active' : 'default'">报表管理</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="5-1">销售出库推送明细表 <i :class=" navIndex === '5-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-2">客户收款推送明细表 <i :class=" navIndex === '5-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-3">费用应收单汇总报表 <i :class=" navIndex === '5-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-4">实际应付单汇总报表 <i :class=" navIndex === '5-4' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-5">预估应付单汇总报表 <i :class=" navIndex === '5-5' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-6">销售汇总报表 <i :class=" navIndex === '5-6' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-7">收款汇总报表 <i :class=" navIndex === '5-7' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="5-8">费用报表 <i :class=" navIndex === '5-8' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="6">
                <template slot="title">
                    <ComIcon :name="!!/^6/.exec(navIndex) ? 'icon9_1' : 'icon9'"></ComIcon>
                    <span :class="!!/^6/.exec(navIndex) ? 'active' : 'default'">操作管理</span>
                </template>
                <el-menu-item-group>
                    <el-submenu index="6-1">
                        <template slot="title">关账与反关账</template>
                        <el-menu-item index="6-1-1">关账</el-menu-item>
                        <el-menu-item index="6-1-2">反关账</el-menu-item>
                    </el-submenu>
                </el-menu-item-group>
                <el-menu-item-group>
                    <el-menu-item index="6-2">汇总 <i :class=" navIndex === '6-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="6-3">异常处理 <i :class=" navIndex === '6-3' ? 'el-icon-caret-left' : ''"></i></el-menu-item>-->
                <!--</el-menu-item-group>-->
            </el-submenu>
            <el-submenu index="7">
                <template slot="title">
                    <ComIcon :name="!!/^7/.exec(navIndex) ? 'icon10_1' : 'icon10'"></ComIcon>
                    <span :class="!!/^7/.exec(navIndex) ? 'active' : 'default'">错误日志</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="7-1">推送错误 <i :class=" navIndex === '7-1' ? 'el-icon-caret-left' : ''"></i></el-menu-item>
                </el-menu-item-group>
                <!--<el-menu-item-group>-->
                    <!--<el-menu-item index="7-2">汇总错误 <i :class=" navIndex === '7-2' ? 'el-icon-caret-left' : ''"></i></el-menu-item>-->
                <!--</el-menu-item-group>-->
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage'
    import { mapGetters, mapActions } from 'vuex';
    import {
        submitCloseAccount,
        submitUnCloseAccount
    } from "@/api/login";
    import routerConfig from '@/router/routerConfig';
    import * as service from '@/services';
    import CONFIG from '@/assets/js/config';

    export default {
        name: "NavPage",
        data() {
            return {
                isShowUpdate: false,
                isCollapse: false,
                activeIndex: '1-1',
                openIndex: '',
                navFnConfig: {

                    // 关账
                    '6-1-1': 'handleCloseAccount',

                    // 反关账
                    '6-1-2': 'handleUnCloseAccount'
                },
                closeAccountData: {
                    operator: '',
                    date: ''
                }
            }
        },
        computed: {
            ...mapGetters(['permitPath', 'navIndex'])
        },
        methods: {
            submitCloseAccount,
            submitUnCloseAccount,
            handleSelectModule(inx) {

                if (this.navFnConfig[inx]) {
                    if (this.permitPath.indexOf(routerConfig.navJumpFn(inx).path) === -1) {
                        this.$message({
                            type: 'warning',
                            message: '对不起，您没有当前操作的权限！'
                        });
                        return;
                    }

                    this[this.navFnConfig[inx]]();
                    return;
                }
                this.activeIndex = inx;
                storage.setSessionStorage('navActive', inx);

                let to = routerConfig.navJumpFn(inx);
                this.$router.push(to.path);
                this.$store.commit('SAVE_TAB_LABELS', {
                    path: to.path,
                    name: to.name,
                    index: inx
                });
                this.$store.commit('SAVE_NAV_INDEX', inx)
            },

            // 关账
            handleCloseAccount() {
                this.submitCloseAccount().then(({ data }) => {
                    this.closeAccountData.date = data.datetime.unclose_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '关账',
                        message: h('p', null, [
                            h('p', null, `确认要对第${showDate}期(${showDate}月数据)进行关账处理吗?`),
                            h('span', null, `关账后，新产生的数据将记录到下期。`)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(action => {
                        let obj = {
                            operator: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_closed: 1
                        };
                        this.submitCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: '操作成功'
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.erorrMess || '操作失败'
                                })
                            }
                        })
                    });
                })
            },

            // 反关账
            handleUnCloseAccount() {

                this.submitUnCloseAccount().then(({ data }) => {
                    this.closeAccountData.date = data.datetime.close_time;

                    let dateStr = this.closeAccountData.date;
                    let showDate = parseInt(dateStr.substring(dateStr.indexOf('-') + 1, dateStr.indexOf('-') + 3));

                    const h = this.$createElement;
                    this.$msgbox({
                        title: '反关账',
                        message: h('p', null, [
                            h('p', null, `确认要对第${showDate}期(${showDate}月数据)进行反关账处理吗?`),
                            h('span', { style: 'color: red' }, '温馨提示:'),
                            h('span', null, `需确认金蝶系统会计期间也在第${showDate}期。`)
                        ]),
                        showCancelButton: true,
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(action => {
                        let obj = {
                            modify_user: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                            datetime: this.closeAccountData.date,
                            is_unclose: 1
                        };
                        this.submitUnCloseAccount(obj).then(({ data }) => {
                            if (data.status) {
                                this.$message({
                                    type: 'success',
                                    message: '操作成功'
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    message: data.errorMess || '操作失败'
                                })
                            }
                        })
                    });
                })
            }
        },
        created() {
            if (process.env.NODE_ENV == 'production') {
                window.parent.postMessage({
                    complete: true
                }, CONFIG.IN_PRODUCTION_PATH)
            } else {
                window.parent.postMessage({
                    complete: true
                }, CONFIG.IN_DEVELOPMENT_PATH)
            }
        },
        watch: {
            isCollapse(val) {
                let view = document.querySelector('.ui-content');
                if (val) {
                    setTimeout(() => {
                        view.style.paddingLeft = '90px';
                    }, 300)
                } else {
                    // this.activeIndex = '1-2';
                    this.$refs.navMenu.updateActiveIndex();
                    view.style.paddingLeft = '220px';
                }
            },
            navIndex(val) {
                this.activeIndex = val;
                this.$refs.navMenu.updateActiveIndex();
            }
        }
    }
</script>

<style scoped lang="less">
    @import (reference) '../../assets/less/index';
    .ui-nav-bar {
        background-color: @base-nav-bgColor;

        .ui-logo {
            float: left;
            margin-left: 26px;
            background-color: @base-nav-bgColor;

            img {
                margin-top: 20px;
            }
        }
    }

    .active {
        color: @base-nav-activeColor;
    }

    .default {
        color: @base-default-color;
    }
</style>


