<template>
    <div>
        <!-- 推送中 -->
        <el-dialog :visible.sync="hintDialogData.pushProofing" center width="200px" :show-close="false">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-time"><i class="el-icon-time"></i></div>
                    <div class="ui-com-hintcss">
                        <div>推送中，请稍等</div>
                        <div>预计<strong class="ui-com-tag">60s</strong></div>
                        <div>请勿关闭窗口</div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <!-- 推送成功 -->
        <el-dialog :visible.sync="hintDialogData.pushSuccess" center width="200px" :show-close="false"
                   @open="autoCloseHint('pushSuccess')">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-succ"><i class="el-icon-success"></i></div>
                    <div class="ui-com-hintcss">
                        <div>推送成功</div>
                        <div v-if="hintDialogData.pushNum >= 2">生成单据236张</div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <!-- 推送失败 -->
        <el-dialog :visible.sync="hintDialogData.pushFail" center width="200px" :show-close="false"
                   @open="autoCloseHint('pushFail')">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-succ"><i class="el-icon-error"></i></div>
                    <div class="ui-com-hintcss">推送失败</div>
                </div>
            </div>
        </el-dialog>

        <!-- 保存成功  -->
        <el-dialog :visible.sync="hintDialogData.saveSuccess" center width="200px" :show-close="false">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-succ"><i class="el-icon-success"></i></div>
                    <div class="ui-com-hintcss">定时推送保存成功</div>
                </div>
            </div>
        </el-dialog>

        <!-- 删除成功 -->
        <el-dialog :visible.sync="hintDialogData.delSuccess" center width="200px" :show-close="false"
                   @open="autoCloseHint('delSuccess')">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-succ"><i class="el-icon-success"></i></div>
                    <div class="ui-com-hintcss">删除成功</div>
                </div>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="hintDialogData.delFail" center width="200px" :show-close="false"
                   @open="autoCloseHint('delFail')">
            <div>
                <div class="ui-com-center">
                    <div class="ui-com-icon-succ"><i class="el-icon-error"></i></div>
                    <div class="ui-com-hintcss">删除失败</div>
                </div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        data() {
            return {};
        },
        props: ['hintdialogdata'],
        computed: {
            hintDialogData() {
                // console.log(this.hintdialogdata);
                return this.hintdialogdata;
            }
        },
        methods: {
            confirmUnPushReceipt() {
                this.$emit('confirmUnPushReceipt');
            },
            // 自动关闭弹窗
            autoCloseHint(name) {
                var _this = this;
                setTimeout(function () {
                    _this.$emit('autoCloseHint', name);
                }, 1500);
            }
        }
    };
</script>



