<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <span>单据日期</span>
                        <el-date-picker
                                size="medium"
                                v-model="queryData.businessDate"
                                type="daterange"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                format="yyyy-MM-dd"
                                value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>原属账期</span>
                        <el-date-picker
                                size="medium"
                                popper-class="ui-layout_date"
                                v-model="queryData.account"
                                type="month"
                                format="yyyy 年第 MM 期"
                                placeholder="请选择"
                                :editable="false"
                                :clearable="false"
                                range-separator="至"
                                value-format="yyyy-MM"
                                @focus="handleFocusDateInput">
                        </el-date-picker>
                    </li>
                    <li>
                        <span>单据类型</span>
                        <el-select
                                size="medium"
                                v-model="queryData.billType"
                                placeholder="请选择">
                            <el-option
                                    v-for="item in optionList.billTypeList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </li>
                    <li>
                        <span>单据编号</span>
                        <el-input v-model="queryData.billNo" placeholder="请输入" size="medium"></el-input>
                    </li>
                    <li class="ui-main-search-btn">
                        <el-button @click="handleResetQueryInfo()" size="medium">重置</el-button>
                        <el-button size="medium" type="primary" @click="handleQueryInfo({limit: pageData.currentList})">查询</el-button>
                    </li>
                </ul>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'drop_down_2' : 'drop_down_1'"></ComIcon></span>
            <el-button
                    v-for="item in buttonList"
                    size="medium"
                    :type="item.type"
                    :key="item.value"
                    :disabled="item.permit(thisVm)"
                    @click="item.action(thisVm, item.method)">{{item.name}}</el-button>
        </div>
        <div class="ui-main-module-table" v-if="initTable">
            <el-table
                    ref="multipleSelection"
                    :data="tableData"
                    class="ui-data-height"
                    border
                    stripe
                    style="width: 100%"
                    :height="tableHeight"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="40">
                </el-table-column>
                <el-table-column
                        type="index"
                        :label="tableKey[0]"
                        :index="autoAddTableInx"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="bill_no"
                        :label="tableKey[1]"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="bill_type_cn"
                        :label="tableKey[2]"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="dates"
                        :label="tableKey[3]">
                </el-table-column>
                <el-table-column
                        prop="account_time_cn"
                        :label="tableKey[4]">
                </el-table-column>
                <el-table-column
                        prop="operator"
                        :label="tableKey[5]">
                    <template slot-scope="scope">
                        <el-button
                                type="primary"
                                plain
                                @click="handleCurrentInfo(scope.row)">反为当期
                        </el-button>
                        <el-button
                                type="primary"
                                plain
                                @click="handleNextInfo(scope.row)">归为下期
                        </el-button>
                    </template>
                </el-table-column>
                <template slot="empty">
                    <div>
                        <NotData v-show="isNotData"></NotData>
                    </div>
                </template>
            </el-table>
        </div>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog
                center width="450px"
                title="反为当期"
                :visible.sync="isCurrentShow"
                :close-on-click-modal="false">
            <div>
                <p>是否确定对第{{thisAccount}}期（{{thisAccount}}月数据）进行反关账？</p>
                <p><span style="color: red">温馨提示:</span>需确保金蝶系统该账期未关账。</p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancelPush()">取消</el-button>
                <el-button type="primary" @click="handleUnCloseAccount()">反关账</el-button>
                <el-button type="primary" @click="handleSumCount()">反关账并汇总</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import * as storage from '@/utils/storage'
    import {
        mapGetters,
        mapActions
    } from 'vuex'
    import {
        submitUnCloseAccount
    } from "@/api/login";
    import config from '@/assets/js/config';
    import {
        getAbnormalInfo,
        submitSumAccount
    } from '@/api/operate_management/operate_abnormal';
    import {
        autoAddTableInx,
        getDefaultTime
    } from '@/services/index'

    export default {
        data() {
            return {

                // 判断是否无数据
                isNotData: false,

                // 保存组件实例，在绑定按钮事件时使用
                thisVm: {},

                // 归期状态
                isCurrentShow: false,

                //搜索缩进
                showSearch: true,

                initTable: false,
                tableHeight: 100,

                // 查询下拉列表
                optionList: {
                    billTypeList: [
                        {value:"1", label:"采购订单"},
                        {value:"2", label:"采购退料单"},
                        {value:"3", label:"采购折扣单"},
                        {value:"4", label:"采购运费单"},
                        {value:"5", label:"采购入库单"},
                        {value:"6", label:"采购付款单"},
                        {value:"7", label:"采购退款单"},
                        {value:"8", label:"销售出库订单"},
                        {value:"9", label:"销售退货订单"},
                        {value:"10", label:"预估应付单"},
                        {value:"11", label:"实际应付单"},
                        {value:"12", label:"应收单"},
                        {value:"13", label:"收款单"},
                        {value:"14", label:"收款退款单"},
                        {value:"15", label:"其他入库单"},
                        {value:"16", label:"其他出库单"},
                        {value:"17", label:"盘盈单"},
                        {value:"18", label:"盘亏单"},
                    ]
                },

                //设置默认时间
                saveDefaultDate: [],

                // 查询参数
                queryData: {
                    businessDate: [],
                    account: '',
                    billType: '1',
                    billNo: ''
                },

                // 列表数据
                tableKey: ["序号","单据编号","单据类型","原单据日期","原属账期","操作"],
                tableData: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                //多选数组
                tableSelectCheck: [],

                thisAccount: '',

                currentParams: {},
                nextParams: {},
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'abnormalInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetAbnormalInfo'
            ]),
            getAbnormalInfo,
            autoAddTableInx,
            submitUnCloseAccount,
            submitSumAccount,
            getDefaultTime,

            handleQueryInfo(params, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['operate_abnormal']);
                }

                let obj = {

                    cbegintime: this.queryData.businessDate[0],
                    cendtime: this.queryData.businessDate[1],
                    bill_type: this.queryData.billType,
                    bill_no: this.queryData.billNo,
                    original_date_time: !this.queryData.account ? this.queryData.account : this.queryData.account + '-01 00:00:00',   // 后台指定格式
                };
                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'operate_abnormal',
                    value: JSON.stringify(this.queryData)
                });

                obj = Object.assign(params || {}, obj);

                this.GetAbnormalInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;
                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },
            //反为当期
            handleCurrentInfo(row){
                //单行
                if(row){
                    this.currentParams.dates = row.account_time_cn;
                }else{

                    let firstData = this.tableSelectCheck[0];
                    //多行
                    if(this.tableSelectCheck.length){
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for(let i = 0; i < len; i++){
                            if (firstData.account_time_cn !== this.tableSelectCheck[i].account_time_cn) {
                                this.$message({
                                    type: 'warning',
                                    message: '不允许跨月操作,请选择相对应原属账期',
                                });
                                paramsArr.splice(0);
                                break;
                            }
                            paramsArr.push(this.tableSelectCheck[i].account_time_cn);
                        }
                        this.currentParams.dates = paramsArr.join(',');
                    }
                }
                if (!this.currentParams.dates) return;

                this.$confirm('请先处理归为下期的单据，否则一旦反关账，该期所有未处理的单据将全部进入当期。确认已处理完所有归为下期的单据？', '反为当期', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true

                }).then(() => {
                    this.submitUnCloseAccount().then(({ data }) =>{
                        this.isCurrentShow = true;
                        if(row){
                            this.thisAccount = parseInt(row.dates.substring(row.dates.indexOf('-') + 1, row.dates.indexOf('-') + 3));
                            this.thisAccountDates = row.dates;
                        }else{
                            this.thisAccount = parseInt(this.tableSelectCheck[0].dates.substring(this.tableSelectCheck[0].dates.indexOf('-') + 1, this.tableSelectCheck[0].dates.indexOf('-') + 3));
                            this.thisAccountDates = this.tableSelectCheck[0].dates;
                        }
                    })
                }).catch(() => {
                    console.log('取消')
                });
            },
            //归为下期
            handleNextInfo(row){
                //单行
                if(row){
                    this.nextParams.billNo = row.bill_no;
                    this.nextParams.dates = row.account_time_cn;

                }else{
                    //多行
                    let firstData = this.tableSelectCheck[0];

                    if(this.tableSelectCheck.length){
                        let len = this.tableSelectCheck.length;
                        let paramsArr = [];
                        for(let i = 0; i < len; i++){

                            if (firstData.account_time_cn !== this.tableSelectCheck[i].account_time_cn) {
                                this.$message({
                                    type: 'warning',
                                    message: '不允许跨月操作,请选择相对应原属账期',
                                });
                                paramsArr.splice(0);
                                break;
                            }
                            paramsArr.push(this.tableSelectCheck[i].bill_no);
                        }
                        this.nextParams.billNo = paramsArr.join(',');
                    }
                }
                if (!this.nextParams.billNo) return;

                this.$confirm('是否确定归为下期？归为下期后，单据日期将变更为下期第一天。', '归为下期', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(action =>{

                    let obj = {
                        is_next: 1,
                        bill_type: this.queryData.billType,
                        bill_no: this.nextParams.billNo,
                        original_date_time: this.thisAccountDates,
                    };
                    this.getAbnormalInfo(obj).then(({data}) =>{
                        if (data.status) {
                            this.$message({
                                type: 'success',
                                message: '该单据已成功归为下期！'
                            });
                            this.handleQueryInfo({limit: this.pageData.currentList});
                        } else {
                            this.$message({
                                type: 'error',
                                message: data.errorMess || '操作失败！'
                            });
                        }
                    })
                })
            },
            //反关账
            handleUnCloseAccount(){

                let obj = {
                    modify_user: JSON.parse(storage.getSessionStorage('userInfo')).uid,
                    datetime: this.thisAccountDates,
                    is_unclose: 1
                };
                this.submitUnCloseAccount(obj).then(({ data }) => {

                    if (data.status) {
                        this.$message({
                            type: 'success',
                            message: '操作成功！'
                        });
                        this.handleQueryInfo({limit: this.pageData.currentList});
                    } else {
                        this.$message({
                            type: 'error',
                            message: data.errorMess || '操作失败！'
                        });
                    }
                })
                this.isCurrentShow = false;
            },
            //反关账并汇总
            handleSumCount(){

                let obj = {
                    is_unclose: 1,
                    original_date_time: this.thisAccountDates,
                };
                this.submitSumAccount(obj).then(({ data }) => {

                    if (data.status) {
                        this.$message({
                            type: 'success',
                            message: '操作成功！'
                        });
                        this.handleQueryInfo({limit: this.pageData.currentList});
                    } else {
                        this.$message({
                            type: 'error',
                            message: data.errorMess || '操作失败！'
                        });
                    }
                });
                this.isCurrentShow = false;
            },
            //取消
            handleCancelPush(){
              this.isCurrentShow = false;
            },
            //勾选方法
            handleSelectionChange: function (val) {
                this.tableSelectCheck = val;
                //console.log(this.tableSelectCheck)
            },
            //重置
            handleResetQueryInfo: function () {
                this.queryData = {
                    businessDate: this.saveDefaultDate,
                    original_date_time: '',
                    billType: '1',
                    billNo: ''
                };
                this.currentPage = 1;
                this.pageSize = config.pageSize;
            },
            handleSizeChange: function (size) {
                this.handleQueryInfo({
                    limit: size,
                    offset: 1
                }, true)
            },
            handleCurrentChange: function (idx) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: idx
                }, true)
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 时间框焦点获取
            handleFocusDateInput() {
                clearInterval(timeout);
                let timeout = setTimeout(() => {
                    let dateEl = document.querySelectorAll('.ui-layout_date .el-month-table td a');
                    dateEl.forEach((e, i) => {
                        e.innerText = e.innerText.replace('月', '期')
                    })
                }, 100)
            },
            //查询回调
            handleQueryInfoCallBack(data) {
                // 判断是否没数据
                this.isNotData = !data.data_list.value.length;

                // 表格数据渲染
                this.tableKey = data.data_list.key;
                this.tableData = data.data_list.value;

                for (let i = 0; i < this.tableData.length; i++) {
                    let str = this.tableData[i].dates;
                    this.tableData[i].account_time_cn = str.substring(0, str.indexOf(' ')).replace('-', '年').replace(/-[0-9]{1,2}/, '期');
                }
                this.optionList.billTypeList = data.data_list.bill_type;

                //获取分页信息
                this.pageData.currentList = data.page_data.limit;
                this.pageData.currentPage = data.page_data.offset;
                this.pageData.currentTotal = data.page_data.total;
            }
        },
        created: function () {
            this.thisVm = this;
            try {
                this.getDefaultTime().then(data => {
                    this.saveDefaultDate.push(data.start, data.end);
                    if (JSON.stringify(this.abnormalInfo) === '{}') {
                        this.queryData.businessDate = this.saveDefaultDate;
                        this.handleQueryInfo();
                    } else {
                        this.queryData = JSON.parse(this.queryParamsInfo['operate_abnormal']);
                        this.handleQueryInfoCallBack(this.abnormalInfo);
                    }
                });
            } catch (e) {
                console.log(e);
            }
        },
        mounted: function () {
            this.tableHeight = config.setTableHeight('ui-main-module');
            this.initTable = true;
        },
        watch: {
            showSearch(val, old) {
                let vm = this;
                document.querySelector('body').style.overflow = 'hidden';
                setTimeout(() => {
                    vm.initTable = false;
                    vm.tableHeight = config.setTableHeight('ui-main-module');
                    vm.initTable = true;
                    document.querySelector('body').style.overflow = '';
                }, 400)
            }
        }
    };
</script>

<style lang="less" scoped>

    @import (reference) '../../../assets/less/index';
    p {
        margin-top: 8px;
        text-align: center;
    }

</style>




